import deployApi from "../services/deployService";

export default {
    namespaced: true,
    state: {
        pageNum: 1,
        pageSize: 6,
        list: [],
        total: 4,
        thirdBussinessManager: {},
        details: {},
        location: "",
        start: "",
        end: "",
        coop: "",
        writeName: ""
    },
    mutations: {
        setData(state, payload) {
            //total整合总条数            
            Object.assign(state, {
                list: payload.thirdBussinessManager.list,
                total: payload.AllCount
            })
        },
    
        //企业管理按条件筛选查询
        initLocation(state, payload) {
            Object.assign(state, {
                location: payload
            })
        },
        initStart(state, payload) {
            Object.assign(state, {
                start: payload
            })
        },
        initEnd(state, payload) {
            Object.assign(state, {
                end: payload
            })
        },
        initCoop(state, payload) {            
            Object.assign(state, {
                coop: payload
            })
        },
        initWriteName(state, payload) {
            Object.assign(state, {
                writeName: payload
            })
        },
        searchData(state, payload) {
            Object.assign(state, {
                list: payload.TBusiness.list,
                total: payload.TBusiness.total
            })
        },
        //分页
        changePage(state, payload) {
            Object.assign(state, {
                pageNum: payload
            })
        },
        //第三方企业详情查看
        checkData(state, payload) {
            Object.assign(state, {
                details: payload
            })
        },
    },
    actions: {
        //列表数据获取
        async deployFormAsync(context) {
            const params = {
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum
            };
            const {data} = await deployApi.deployForm(params);
            context.commit("setData", data)
            return data;
        },
        //第三方企业查询 按条件
        async searchAsync(context) {
            const params = {
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum,
                startT: context.state.start,
                endT: context.state.end,
                state: context.state.coop,
                address: context.state.location,
                bname: context.state.writeName,
            };            
            const {data} = await deployApi.search(params);            
            context.commit("searchData", data);
        },
        //重启第三方企业合作
        async reStartAsync(context, payload) {
            const data = await deployApi.reStart(payload);
            return data;
        },
        //暂停第三方企业合作
        async pauseAsync(context, payload) {
            const data = await deployApi.pause(payload);
            return data;
        },

        //第三方企业详情查看
        async thirdDetailAsync(context, payload) {
            const {
                data
            } = await deployApi.thirdDetail(payload);
            context.commit("checkData", data)
            return data;
        },
        //添加第三方企业
        async addThirdAsync(context, payload) {
            const data = await deployApi.addThird(payload);
            return data;
        },
        //编辑更新第三方企业
        async updateThirdAsync(context, payload) {
            const data = await deployApi.updateThird(payload);
            return data;
        },
    }
}