<template>
  <div class="othersContainer">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Index/deploy' }">独立部署授权管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加第三方企业</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="form">
      <el-form :model="ruleForm" ref="ruleForm" label-width="240px" class="demo-ruleForm">
        <el-form-item label="企业名称" prop="bname" required>
          <el-input v-model="ruleForm.bname" placeholder="限20字"></el-input>
        </el-form-item>
        <el-form-item label="主账号" prop="mainAccount">
          <el-input v-model="ruleForm.mainAccount" placeholder="3-10位数字、字母、字符"></el-input>
        </el-form-item>
        <p class="ptips">主账号添加后不可修改，且不可重复，初始密码与账号相同</p>
        <el-form-item label="企业地址" prop="address">
          <label>
            <el-cascader
              v-model="ruleForm.area"
              :options="areaOption"
              @change="handleChange"
              clearable
            ></el-cascader>
          </label>
          <el-input v-model="ruleForm.address" placeholder="请输入详细地址" id="suggestId" style="marginTop:15px;"></el-input>
        </el-form-item>
        <el-form-item style="marginTop:-15px;">
          <baidu-map
            :center="center"
            :zoom="zoom"
            @ready="handler"
            class="baiduMap"
            :scroll-wheel-zoom="true"
            id="l-map"
          >
            <!-- <bm-view class="map"></bm-view> -->
            <bm-local-search :keyword="arealocation" :auto-viewport="true" :panel="false"></bm-local-search>
            <bm-local-search
              :keyword="ruleForm.address"
              :auto-viewport="true"
              :panel="false"
              @searchcomplete="getGps"
            ></bm-local-search>
          </baidu-map>
        </el-form-item>
        <el-form-item label="负责人" prop="personInCharge" required>
          <el-input v-model="ruleForm.personInCharge" placeholder="限20字"></el-input>
        </el-form-item>
        <el-form-item label="负责人电话" prop="picPhone" required>
          <el-input v-model="ruleForm.picPhone"></el-input>
        </el-form-item>
        <el-form-item label="服务到期时间" required>
          <el-date-picker
            v-model="ruleForm.validityTime"
            type="datetime"
            placeholder="选择日期时间"
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="合作状态" prop="state" required>
          <el-select v-model="ruleForm.state" placeholder="请选择合作状态">
            <el-option label="合作中" value="0"></el-option>
            <el-option label="暂停合作" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="其他说明" prop="description">
          <el-input
            type="textarea"
            v-model="ruleForm.description"
            placeholder="限500字"
            maxlength="500"
          ></el-input>
        </el-form-item>
        <el-form-item label="终端账号数限制" prop="deviceNumLimit" required>
          <el-input v-model="ruleForm.deviceNumLimit"></el-input>
        </el-form-item>
        <div class="second">
          <el-form-item label="功能配置" required>
            <div class="checkGroup">
              <el-checkbox label="语音通话功能" v-model="ruleForm.voice" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="视频通话功能" v-model="ruleForm.see" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="对讲功能" v-model="ruleForm.talk" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="电子围栏功能" v-model="ruleForm.rail" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="轨迹功能" v-model="ruleForm.track" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="广播功能" v-model="ruleForm.broad" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="无声侦护功能" v-model="ruleForm.detect" true-label="0" false-label="1"></el-checkbox>
            </div>
          </el-form-item>
          <el-form-item label="终端视频录制时长限制" prop="time" required>
            <el-radio-group v-model="ruleForm.time" @change="finalTime">
              <el-radio label="无限制"></el-radio>
              <el-radio label="限制"></el-radio>
            </el-radio-group>
            <el-input placeholder="200" :disabled="one" v-model="ruleForm.timeLimit"></el-input>
            <span>分钟</span>
            <i>（到达该时长自动结束录制，最大不可超过1440分钟）</i>
          </el-form-item>
          <el-form-item label="终端视频自动上传限制" prop="upload" required>
            <el-radio-group v-model="ruleForm.upload" @change="videoUpload">
              <el-radio label="无限制"></el-radio>
              <el-radio label="限制"></el-radio>
            </el-radio-group>
            <el-input placeholder="1000" :disabled="two" v-model="ruleForm.videoLimit"></el-input>
            <span>MB</span>
            <i>（大于该值的视频不自动上传）</i>
          </el-form-item>
          <el-form-item label="调度员app视频自动上传限制" prop="app" required>
            <el-radio-group v-model="ruleForm.app" @change="appUpload">
              <el-radio label="无限制"></el-radio>
              <el-radio label="限制"></el-radio>
            </el-radio-group>
            <el-input placeholder="1000" :disabled="three" v-model="ruleForm.appLimit"></el-input>
            <span>MB</span>
            <i>（在非wifi网络下大于该值的视频不自动上传，wifi网络下不受限制）</i>
          </el-form-item>
          <el-form-item label="终端日志记录开关" prop="switch" required>
            <el-radio-group v-model="ruleForm.switch" @change="logSwitch">
              <el-radio label="关"></el-radio>
              <el-radio label="开"></el-radio>
            </el-radio-group>
            <el-input placeholder="5" :disabled="four" v-model="ruleForm.switchLimit"></el-input>
            <span>天自动清理</span>
            <i>（如设置5天，则每5天清空日志；设置为0时不自动清理）</i>
          </el-form-item>
          <el-form-item label="调度员app日志记录开关" prop="log" required>
            <el-radio-group v-model="ruleForm.log" @change="appLog">
              <el-radio label="关"></el-radio>
              <el-radio label="开"></el-radio>
            </el-radio-group>
            <el-input placeholder="5" :disabled="five" v-model="ruleForm.appLogLimit"></el-input>
            <span>天自动清理</span>
            <i>（如设置5天，则每5天清空日志；设置为0时不自动清理）</i>
          </el-form-item>
          <el-form-item label="企业存储空间报警配置" prop="save" required>
            <el-radio-group v-model="ruleForm.save" @change="savation">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="自定义"></el-radio>
            </el-radio-group>
            <el-input placeholder="500" :disabled="six" v-model="ruleForm.saveLimit"></el-input>
            <span>GB</span>
            <i>（企业存储空间大于该值则会触发一次报警通知）</i>
          </el-form-item>
        </div>
        <el-form-item label="绑定服务器IP" prop="serverHost" required>
          <el-input v-model="ruleForm.serverHost"></el-input>
        </el-form-item>
        <el-form-item label="绑定服务器MAC地址" prop="serverMac" required>
          <el-input v-model="ruleForm.serverMac"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="add">保存</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { regionData, CodeToText, TextToCode } from "element-china-area-data";
import { createNamespacedHelpers } from "vuex";
import { BmlMarkerClusterer } from "vue-baidu-map";
const { mapState, mapActions } = createNamespacedHelpers("deploy");
export default {
  data() {
    return {
      ruleForm: {
        bname: "",
        mainAccount: "",
        area: "",
        address: "",
        personInCharge: "",
        picPhone: "",
        state: "",
        description: "",
        deviceNumLimit: "",
        serverHost: "",
        serverMac: "",
        validityTime: "",
        voice: 1,
        see: 1,
        talk: 1,
        rail: 1,
        track: 1,
        broad: 1,
        detect: 1,
        time: "",
        timeLimit: 60,
        upload: "",
        videoLimit: 1000,
        app: "",
        appLimit: 500,
        switch: "",
        switchLimit: 20,
        log: "",
        appLogLimit: 20,
        save: "",
        saveLimit: 100
      },
      areaOption: regionData,
      center: { lng: 104, lat: 30 },
      zoom: 13,
      one: true,
      two: true,
      three: true,
      four: true,
      five: true,
      six: true,
      location: "北京",
      keyword: "百度",
      arealocation: ""
    };
  },
  methods: {
    ...mapActions(["addThirdAsync", "deployFormAsync"]),
    handleChange(value) {
      let province = CodeToText[value[0]];
      let city = CodeToText[value[1]];
      let area = CodeToText[value[2]];
      this.arealocation = province + city + area;
      // console.log(this.arealocation);
      this.ruleForm.address = this.arealocation;
      // console.log(this.ruleForm.address);
    },
    //终端视频录制时长限制
    finalTime() {
     if (this.ruleForm.time == "无限制") {
        this.videoTime = 1;
        this.ruleForm.timeLimit = "";
        this.one = true;
      } else if (this.ruleForm.time == "限制") {
        this.videoTime = 2;
        this.one = false;
      }
    },
    //终端视频自动上传限制
    videoUpload() {
      if (this.ruleForm.upload == "无限制") {
        this.uploadVideo = 1;
        this.ruleForm.videoLimit = "";
        this.two = true;
      } else if (this.ruleForm.upload == "限制") {
        this.uploadVideo = 2;
        this.two = false;
      }
    },
    //调度员app视频自动上传限制
    appUpload() {
     if (this.ruleForm.app == "无限制") {
        this.appVideo = 1;
        this.ruleForm.appLimit = "";
        this.three = true;
      } else if (this.ruleForm.app == "限制") {
        this.appVideo = 2;
        this.three = false;
      }
    },
    //终端日志记录开关
    logSwitch() {
      if (this.ruleForm.switch == "关") {
        this.switchLog = 1;
        this.ruleForm.switchLimit = "";
        this.four = true;
      } else if (this.ruleForm.switch == "开") {
        this.switchLog = 2;
        this.four = false;
      }
    },
    //调度员app日志记录开关
    appLog() {
       if (this.ruleForm.log == "关") {
        this.logApp = 1;
        this.ruleForm.appLogLimit = "";
        this.five = true;
      } else if (this.ruleForm.log == "开") {
        this.logApp = 2;
        this.five = false;
      }
    },
    //企业存储空间报警配置
    savation() {
      if (this.ruleForm.save == "跟随系统设置") {
        this.saveSet = 0;
        this.ruleForm.saveLimit = "";
        this.six = true;
      } else if (this.ruleForm.save == "自定义") {
        this.saveSet = 1;
        this.six = false;
      }
    },
    async add() {
      let bname = this.ruleForm.bname;
      let mainAccount = this.ruleForm.mainAccount;
      let locations = this.ruleForm.address;
      let lat = this.gpsLocation.lat;
      let lon = this.gpsLocation.lng;
      //合并地址
      const data = await this.addThirdAsync({
        ...this.ruleForm,
        address: locations,
        longitude: lon,
        latitude: lat
      });
      if (data.code == 200) {
        this.$message("添加成功");
        this.deployFormAsync();
      } else if (data.code == 202) {
        this.$message("用户名已经存在");
      } else if (data.code == 207) {
        this.$message("主账号格式错误");
      }
    },
    //百度地图
    handler({ BMap, map }) {
      var point = new BMap.Point(104.081548, 30.652613);
      map.centerAndZoom(point, 16);
    },
    getGps(results) {
       console.log(results.Br[0].point);
      this.gpsLocation = results.Br[0].point;
    }
  }
};
</script>

<style scoped>
.othersContainer {
  width: 96%;
  margin: 0 auto;
}
.form {
  width: 100%;
  height: 770px;
  overflow-y: auto;
  padding-top: 15px;
  background-color: #ffffff;
}
.button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 140px;
  height: 40px;
  text-align: center;
  padding: 10px 15px;
  margin-left: 123px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.button:hover {
  cursor: pointer;
}
.form input {
  margin-left: 30px;
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 85px;
  height: 32px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 10px;
}
.form span {
  display: inline-block;
  margin-left: 10px;
}
.form i {
  display: inline-block;
  font-style: normal;
  margin-left: 30px;
  color: rgba(153, 153, 153, 1);
}
.form p {
  color: rgba(153, 153, 153, 1);
}
.ptips {
  color: rgba(153, 153, 153, 1);
  margin-left: 240px;
  font-size: 14px;
}
/* 地图容器 */
#l-map {
  width: 400px;
  height: 300px;
  margin-top: 20px;
}
</style>

<style>
.othersContainer .el-breadcrumb {
  text-align: center;
  line-height: 30px;
  padding-top: 10px;
}
.othersContainer .el-breadcrumb__inner a,
.el-breadcrumb__inner.is-link {
  font-size: 20px;
}
.othersContainer .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  font-size: 16px;
}

.form .el-form .el-input__inner {
  width: 400px;
}
.form .el-form .el-form-item__content {
  width: 800px;
}
.form .el-form .address {
  margin-top: 15px;
}
.othersContainer .el-textarea {
  width: 400px;
}
</style>