import systemApi from "../services/systemService";

export default {
    namespaced: true,
    state: {
        pageNum: 1,
        pageSize: 5,
        list: [],
        total: 4,
    },
    mutations: {
        setData(state, payload) {
            //total整合总条数
            Object.assign(state, {
                list: payload.repMap.list,
                total:payload.repMap.total
            })
        },
        changePage(state, payload) {
            Object.assign(state, {
                pageNum: payload
            })
        },
    },
    actions: {
        //列表数据获取
        async mesCenterAsync(context, payload) {
            const params = {
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum
            };  
            const {
                data
            } = await systemApi.mesCenter(params);            
            context.commit("setData", data)
            return data;
        },
        //分页
        async changePageAsync(context, payload) {
            context.commit("changePage", payload);
            const params = {
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum
            };            
            const {data} = await systemApi.mesCenter(params);
            context.commit("setData", data);
        },
        //消息中心-更新已读
        async deletAsync(context, payload) {
            const data = await systemApi.delet(payload);
            return data;
        },
        //系统设置-其他设置-查询
        async otherSetAsync(context, payload) {
            const {data} = await systemApi.otherSet(payload);
            return data;
        },
        //系统设置-其他设置-修改
        async otherUpdateAsync(context, payload) {            
            const data = await systemApi.otherUpdate(payload);
            return data;
        },
        //消息中心状态
        async newsStatusAsync(context,payload){            
            const {data} =await systemApi.newsStatus(payload)                          
            return data;             
        },
    }
}