<template>
  <div class="pageSet">
    <p>页面设置</p>
    <el-form ref="form" :model="form" label-width="140px">
      <el-form-item label="系统名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="系统图标" >
        <el-upload
          class="upload-demo"
          ref="uploadmeta"
          multiple
          :on-preview="systemPicturePreview"
          :on-remove="handleRemove"
          :file-list="iconList"
          :before-upload="beforeIconUpload"
          action
          :http-request="uploadIcon"
          :limit="systemPictureLimit"
          :on-exceed="systemPictureError"
          list-type="picture-card"
        >
          <img class="img-upload" :src="logoUrl"/>
          <i class="el-icon-plus"></i>
          <div class="el-upload__tip" slot="tip">用于在浏览器标签上显示，仅支持常规png、jpg格式，图片小于500KB，建议比例为1:1</div>
          <el-dialog :visible.sync="systemPictureVisible">
            <img width="100%" :src="systemPictureUrl" alt />
          </el-dialog>
        </el-upload>
      </el-form-item>
      <el-form-item label="管理后台LOGO">
        <el-upload
          class="upload-demo"
          ref="uploadlogo"
          :on-preview="logoPreview"
          multiple
          :file-list="logoList"
          :before-upload="beforeLogoUpload"
          action
          :http-request="uploadLogo"
          :limit="logoLimit"
          list-type="picture-card"
          :on-exceed="logoError"
        >
          <img class="img-upload" :src="metaUrl"/>
          <i class="el-icon-plus"></i>
          <div class="el-upload__tip" slot="tip">用于顶部导航栏上显示，仅支持常规png、jpg格式，图片小于1M，建议尺寸120*40 px</div>
         <el-dialog :visible.sync="logoVisible">
            <img width="100%" :src="logoUrl" alt />
          </el-dialog>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitUpload">保存修改</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import axios from "axios";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("center");
export default {
  data() {
    return {
      form: {
        name: ""
      },
      iconList: [],
      logoList: [],
      uploadData: new FormData(),
      systemPictureUrl: "",
      systemPictureVisible: false,
      logoUrl: "",
      logoVisible: false,
      systemPictureLimit:1,
      logoLimit:1,
      logoUrl: '',
      metaUrl: ''
    };
  },
  computed: {
    ...mapState([
      "backgroundUrl"
    ]),
  },
  mounted() {
    this.getLogo();
  },
  methods: {
    ...mapActions([
      "logoMeta",
    ]),
    // 初始化图片
    async getLogo() {
      await this.logoMeta();
      this.logoUrl = this.$download + this.backgroundUrl.data.logoUrl;
      this.metaUrl = this.$download + this.backgroundUrl.data.metaUrl;
    },
    systemPicturePreview(file) {
      this.systemPictureUrl = file.url;
      this.systemPictureVisible = true;
    },
    logoPreview(file) {
      this.logoUrl = file.url;
      this.logoVisible = true;
    },
    /**
     *  删除文件
     */
    handleRemove(file, iconList) {
      console.log(file, iconList);
    },
    /**
     *  限制上传文件
     */
    //系统图标
    systemPictureError(file,iconList){
        if (this.iconList.length==0) {
        this.$message("一次只能上传一张图片")
      }
    },
    beforeIconUpload(file,iconList) {
      // console.log(file)
      // const isJPG = file.type === 'image/jpg'
      let isJPG = false;
      if (
        file.type === "image/jpg" ||
        file.type === "image/png" ||
        file.type === "image/jpeg"
      ) {
        isJPG = true;
      } else {
        isJPG = false;
      }
      const isLt2M = file.size / 1024 / 1024 < 0.5;

      if (!isJPG) {
        this.$message.error("仅支持常规png、jpg格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传图片大小不能超过 500KB!");
      }
      return isJPG && isLt2M;
    },
    //logo
    logoError(file,logoList){
        if (this.logoList.length==0) {
        this.$message("一次只能上传一张图片")
      }
    },
    beforeLogoUpload(file) {
      // const isJPG = file.type === 'image/jpg';
      
      let isJPG = false;
      if (
        file.type === "image/jpg" ||
        file.type === "image/png" ||
        file.type === "image/jpeg"
      ) {
        isJPG = true;
      } else {
        isJPG = false;
      }
      const isLt2M = file.size / 1024 / 1024 < 1;

      if (!isJPG) {
        this.$message.error("仅支持常规png、jpg格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传图片大小不能超过 1MB!");
      }
      return isJPG && isLt2M;
    },
    // 覆盖上传，自定义上传
    uploadIcon(file) {
      this.uploadData.append("meta", file.file);
    },
    uploadLogo(file) {
      this.uploadData.append("logo", file.file);
    },
    // 提交上传文件
    async submitUpload() {
      // this.$refs.uploadmeta.submit();
      // this.$refs.uploadlogo.submit();
      // let parmas = {
      //   logo: this.LogoData,
      //   meta: this.IconData,
      //   systemName: this.form.name
      // }
      this.uploadData.append("systemName", this.form.name);
      console.log(this.uploadData.get("logo"));
      const data = await axios.post(
        "/system/handleAddPageSetting",
        this.uploadData
      );
      if (data.status === 200) {
        this.$message({
          message: "修改成功!",
          type: "success"
        });
        this.getLogo()
      } else {
        this.$message.error("修改失败！");
      }
      console.log(data);
    }
  }
};
</script>

<style scoped>
.pageSet {
  width: 96%;
  height: 100%;
  margin: 0 auto;
}
.pageSet > p {
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(192, 188, 188);
}
</style>

<style >
.pageSet .el-form .el-input__inner {
  width: 500px;
}
.upload-demo {
  position: relative;
}
.pageSet .upload-demo .el-upload {
  margin-left: 230px;
  position: relative;
}
.pageSet .img-upload {
  position: absolute;
  top: 0;
  left: -230px;
  width: 148px;
  height: 148px;
  border: 1px dashed #c0ccda;
  border-radius: 8px;
  overflow: hidden;
}
</style>