<template>
  <div class="logContainer">
    <p>操作日志</p>
    <div class="top">
      <div class="left">
        <div>
          <span>操作类型</span>
          <el-select v-model="value" filterable placeholder="请选择" @change="choose">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </div>
        <div>
          <span>时间段</span>
          <el-date-picker
            v-model="startTime"
            type="datetime"
            placeholder="选择日期时间"
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
          <span class="to">至</span>
          <el-date-picker
            v-model="endTime"
            type="datetime"
            placeholder="选择日期时间"
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </div>
        <button class="query" @click="query">查询</button>
      </div>
      <a
        class="checkout"
        @click="exportLogs"
      >导出记录</a>
    </div>
    <div id="thisTable">
      <el-table :data="list" border style="width: 100%">
        <el-table-column prop="lid" label="id" v-if="show"></el-table-column>
        <el-table-column prop="executor" label="操作者"></el-table-column>
        <el-table-column prop="taskType" label="操作类型" :formatter="changeStatus"></el-table-column>
        <el-table-column prop="executDescribe" label="操作行为" width="500px"></el-table-column>
        <el-table-column prop="relatedData" label="相关数据" width="500px"></el-table-column>
        <el-table-column prop="ctime" label="时间"></el-table-column>
      </el-table>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="pageSize"
        :current-page="pageNum"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
import FileSaver from "file-saver";
import XLSX from "xlsx";
import qs from "qs";
const { mapState, mapActions } = createNamespacedHelpers("appUpdate");
export default {
  data() {
    return {
      options: [
        {
          value: "",
          label: "全部"
        },
        {
          value: 0,
          label: "增加"
        },
        {
          value: 1,
          label: "删除"
        },
        {
          value: 2,
          label: "更新"
        }
      ],
      value: "",
      value1: "",
      show: false,
      startTime: "",
      endTime: "",
      exportLog:''
    };
  },
  mounted() {
    this.logFormAsync();
    this.exportLog=this.$download+"/log/handleExportLog";    
  },
  computed: {
    ...mapState(["pageNum", "pageSize", "list", "total"])
  },
  methods: {
    ...mapActions(["logFormAsync", "changePageAsync"]),
    choose() {
      console.log(this.$store);
      this.$store.commit("appUpdate/initTaskValue", this.value);
      this.logFormAsync();
    },
    //分页
    handleCurrentChange(val) {
      this.changePageAsync(val);
    },
    //查询
    query() {
      this.$store.commit("appUpdate/initStartTime", this.startTime);
      this.$store.commit("appUpdate/initEndTime", this.endTime);
      this.logFormAsync();
    },
    //导出日志
   async exportLogs() {
      const { data: res } = await this.$http.post(
        "/log/handleExportLog",
        qs.stringify(''),
        { responseType: "blob" }
      );
      console.log(res);
      
      const link = document.createElement("a");
      let blob = new Blob([res], { type: "application/vnd.ms-excel" });
      link.style.display = "none";
      link.href = URL.createObjectURL(blob);
      let num = "";
      for (let i = 0; i < 10; i++) {
        num += Math.ceil(Math.random() * 10);
      }
      link.setAttribute("download", "操作日志" + num + ".xls");
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    },
    changeStatus(row, column) {
      switch (row.taskType) {
        case 0:
          return "增加";
          break;
        case 1:
          return "删除";
          break;
        case 2:
          return "更新";
          break;
      }
    }
  }
};
</script>

<style scoped>
.logContainer {
  width: 96%;
  height: 100%;
  margin: 0 auto;
}
.logContainer > p {
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(192, 188, 188);
}
.top {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
}
.left {
  display: flex;
}
.left > div {
  margin-right: 20px;
}
.query,
.checkout {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  height: 34px;
  padding: 0;
  text-align: center;
  margin-top: 3px;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
  text-decoration: none;
  line-height: 34px;
}
.query,
.checkout:hover {
  cursor: pointer;
}
.query {
  width: 70px;
}
.checkout {
  width: 100px;
}
.logContainer span {
  font-size: 14px;
  margin-right: 20px;
}
.el-pagination {
  text-align: center;
  margin-top: 15px;
}
.to {
  margin-left: 20px;
}
</style>