<template>
  <div class="company">
    <p>企业管理</p>
    <div class="nav">
      <div class="nav-top">
        <div class="state">
          <span>合作状态</span>
          <el-select v-model="cooperateValue" @change="cooperation($event)" placeholder="请选择">
            <el-option
              :options="cooperate"
              v-for="item in cooperate"
              :key="item.cooperateValue"
              :label="item.label"
              :value="item.cooperateValue"
            ></el-option>
          </el-select>
        </div>
        <div class="area">
          <span class="demonstration">地区</span>
          <el-cascader v-model="area" :options="areaOption" @change="handleChange" :clearable="clearArea"></el-cascader>
        </div>
        <div class="timeBetween">
          <span>到期时间</span>
          <el-date-picker
            v-model="value2"
            type="date"
            placeholder="选择日期"
            @change="date1"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
          <span class="to">至</span>
          <el-date-picker
            v-model="value4"
            type="date"
            placeholder="选择日期"
            @change="date2"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </div>

        <div class="state">
          <span>检索</span>
          <el-select v-model="retrieveValue" @change="retrie($event)" placeholder="请选择">
            <el-option
              :options="retrieve"
              v-for="item in retrieve"
              :key="item.retrieveValue"
              :label="item.label"
              :value="item.retrieveValue"
            ></el-option>
          </el-select>
        </div>
        <input type="text" v-model="writeName" placeholder="请输入企业名称" v-if="queryname" />
        <input type="text" v-model="writePwd" placeholder="请输入账号" v-else-if="querypwd" />
        <button class="search" @click="search">查询</button>
        <button @click="add">添加企业</button>
      </div>
      <div class="nav-bottom">
        <el-table :data="list" border style="width: 100%">
          <el-table-column prop="bid" label="id" v-if="show"></el-table-column>
          <el-table-column prop="bname" label="企业名称"></el-table-column>
          <el-table-column prop="address" label="地区" width="400px"></el-table-column>
          <el-table-column prop="main_account" label="主账号"></el-table-column>
          <el-table-column prop="validity_time" label="服务器到期时间" value-format="yyyy-MM-dd HH:mm:ss"></el-table-column>
          <el-table-column prop="status" label="状态" :formatter="changeStatus"></el-table-column>
          <el-table-column fixed="right" label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="check(scope.row)">查看</el-button>
              <el-button type="text" size="small" @click="update(scope.row.bid)">编辑</el-button>
              <el-button type="text" size="small" @click="reset = true;rePwd(scope.row.bid)">重置密码</el-button>
              <el-button
                type="text"
                size="small"
                v-if="flag==scope.row.status"
                @click="stop = true;repause(scope.row.bid)"
              >暂停</el-button>
              <el-button
                type="text"
                size="small"
                v-else
                @click="startUse = true;restart(scope.row.bid)"
              >启用</el-button>
              <el-dialog title="启用企业" :visible.sync="startUse" append-to-body width="30%">
                <el-form :model="form2">
                  <el-form-item label="服务到期时间" required>
                    <el-date-picker
                      v-model="form2.dateTime"
                      type="datetime"
                      placeholder="选择日期时间"
                      value-format="yyyy-MM-dd HH:mm:ss"
                    ></el-date-picker>
                  </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                  <el-button @click="startUse = false">取 消</el-button>
                  <el-button type="primary" @click="startUse = false;start();">确 定</el-button>
                </div>
              </el-dialog>
              <el-dialog title="提示" :visible.sync="stop" width="30%" append-to-body>
                <span>确认暂停合作？暂停合作后，该企业下所有账号不可登录服务,确认后执行操作</span>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="stop = false">取 消</el-button>
                  <el-button type="primary" @click="stop = false;pause()">确 定</el-button>
                </span>
              </el-dialog>
              <el-dialog title="提示" :visible.sync="reset" width="30%" append-to-body>
                <span>确认重置为初始密码？</span>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="reset = false">取 消</el-button>
                  <el-button type="primary" @click="reset = false;pwd()">确 定</el-button>
                </span>
              </el-dialog>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          layout="prev, pager, next"
          :total="total"
          :page-size="pageSize"
          :current-page="pageNum"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { regionDataPlus, CodeToText, TextToCode } from "element-china-area-data";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("company");
export default {
  data() {
    return {
      area: [],
      areaOption: regionDataPlus,
      cooperate: [
        {
          cooperateValue: "全部",
          label: "全部"
        },
        {
          cooperateValue: "合作中",
          label: "合作中"
        },
        {
          cooperateValue: "暂停合作",
          label: "暂停合作"
        }
      ],
      retrieve: [
        {
          retrieveValue: "企业名称",
          label: "企业名称"
        },
        {
          retrieveValue: "主账号",
          label: "主账号"
        }
      ],
      cooperateValue: "",
      value2: "",
      retrieveValue: "",
      value4: "",
      show: false,
      flag: false,
      startUse: false,
      form2: {
        dateTime: ""
      },
      formLabelWidth: "120px",
      stop: false,
      reset: false,
      writePwd: "",
      writeName: "",
      queryname: true,
      querypwd: false,
      clearArea:false
    };
  },
  mounted() {
    this.formAsync();
    this.locationAsync({
      parentId: 1
    });
  },
  computed: {
    ...mapState(["pageNum", "pageSize", "list", "total"])
  },
  methods: {
    ...mapActions([
      "formAsync",
      "searchAsync",
      "reStartAsync",
      "pauseAsync",
      "locationAsync",
      "rePasswordAsync"
    ]),
    //合作状态
    cooperation(event, item) {
      if (event == "合作中") {
        this.coop = 0;
      } else if (event == "暂停合作") {
        this.coop = 1;
      }else if (event == "全部") {
        this.coop = "";
      }
    },
    //地区获取
    handleChange(value) {
     let province;
      let city;
      let area;
      // console.log(value[0]);
      if (CodeToText[value[0]] == "全部") {
        province = "";
        city = "";
        area = "";
        this.location = province + city + area;
      } else {
        province = CodeToText[value[0]];

        if (CodeToText[value[1]] == "全部") {
          city = "";
          area = "";
          this.location = province + city + area;
        } else {
          city = CodeToText[value[1]];
          if (CodeToText[value[2]] == "全部") {
            area = "";
            this.location = province + city + area;
          } else {
            area = CodeToText[value[2]];
            this.location = province + city + area;
          }
        }
      }
    },
    //到期时间
    date1(value) {
      this.startTime = value;
      // console.log(this.startTime);
    },
    date2(value) {
      this.endTime = value;
      // console.log(this.endTime);
    },
    //检索
    retrie(event, item) {
      if (event == "企业名称") {
        this.queryname = true;
        this.querypwd = false;
        console.log(event);
      } else if (event == "主账号") {
        this.queryname = false;
        this.querypwd = true;
        console.log(event);
      }
    },
    //添加企业
    add() {
      this.$router.push(`/Index/addition`);
    },
    //查看
    check(prop) {
      sessionStorage.setItem("company", JSON.stringify(prop));
      this.$router.push(`/Index/companyDetails`);
    },
    //更新
    update(prop) {
      sessionStorage.setItem("updateID", JSON.stringify(prop));
      this.$router.push(`/Index/companyUpdate`);
    },
    changeStatus(row, column) {
      switch (row.status) {
        case 0:
          return "合作中";
          break;
        case 1:
          return "暂停合作";
          break;
      }
    },

    //启用
    restart(prop) {
      sessionStorage.setItem("ID", JSON.stringify(prop));
    },
    async start() {
      let id = JSON.parse(sessionStorage.getItem("ID"));
      let dateTime = this.form2.dateTime;
      const data = await this.reStartAsync({
        bid: id,
        endTime: dateTime
      });
      if (data.code === 200) {
        this.flag == true;
        this.$message("重启成功");
        this.formAsync();
      } else if (data.code === 206) {
        this.$message("输入时间不合理,必须为当前时间以后,请重新输入");
      }
    },
    //暂停
    repause(prop) {
      sessionStorage.setItem("BID", JSON.stringify(prop));
    },
    async pause() {
      let id = JSON.parse(sessionStorage.getItem("BID"));
      const data = await this.pauseAsync({
        bid: id
      });
      if (data.code === 200) {
        this.flag == false;
        this.$message("暂停成功");
        this.formAsync();
      } else if (data.code === 201) {
        this.$message("修改失败，联系管理员");
      }
    },
    //重置密码
    rePwd(prop) {
      sessionStorage.setItem("pID", JSON.stringify(prop));
    },
    async pwd() {
      let pid = JSON.parse(sessionStorage.getItem("pID"));
      const data = await this.rePasswordAsync({ bid: pid });
      if (data.code === 200) {
        this.$message("重置成功");
      }
    },
    //查询
    search() {
      let location = this.location;
      let start = this.startTime;
      let end = this.endTime;
      let coop = this.coop;
      let writePwd = this.writePwd;
      let writeName = this.writeName;
      this.$store.commit("company/initLocation", location);
      this.$store.commit("company/initStart", start);
      this.$store.commit("company/initEnd", end);
      this.$store.commit("company/initCoop", coop);
      this.$store.commit("company/initWritePwd", writePwd);
      this.$store.commit("company/initWriteName", writeName);   
      this.searchAsync();
    },
    //分页
    handleCurrentChange(val) {
      this.$store.commit("company/changePage", val);
      this.formAsync();
    }
  }
};
</script>

<style scoped>
.company {
  width: 96%;
  height: 100%;
  margin: 0 auto;
}
p {
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(192, 188, 188);
}
.nav {
  width: 100%;
  height: 800px;
}
.nav-top {
  width: 100%;
  height: 100px;
  display: flex;
}
input {
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 150px;
  height: 34px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 20px;
  margin-top: 1px;
}
.nav-top button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 100px;
  height: 34px;
  text-align: center;
  margin-left: 30px;
  margin-top: 3px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.nav-top button:hover {
  cursor: pointer;
}
.nav-top button:nth-of-type(1) {
  width: 80px;
}
.nav-top > div {
  margin-right: 15px;
}
.nav-top span {
  margin-right: 15px;
  font-size: 14px;
  font-weight: 500;
}
.nav-bottom {
  width: 96%;
  height: 600px;
  margin: 0 auto;
}
.el-pagination {
  text-align: center;
  margin-top: 15px;
}

.nav-top .el-input {
  width: 150px;
}
.timeBetween .el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 160px;
}
.to {
  margin-left: 15px;
}
</style>

<style>
.company .nav-top .state .el-input {
  width: 150px;
}
.area .el-input {
  width: 220px;
}
</style>