<template>
  <div class="deploy">
    <p>独立部署授权管理</p>
    <div class="nav">
      <div class="nav-top">
        <div>
          <span>合作状态</span>
          <el-select v-model="cooperateValue" placeholder="请选择" >
            <el-option
              :options="cooperate"
              v-for="item in cooperate"
              :key="item.cooperateValue"
              :label="item.label"
              :value="item.cooperateValue"
            ></el-option>
          </el-select>
        </div>
        <div class="area">
          <span class="demonstration">地区</span>
          <el-cascader v-model="area" :options="areaOption" @change="handleChange"></el-cascader>
        </div>
        <div class="timeBetween">
          <span>到期时间</span>
          <el-date-picker
            v-model="value2"
            type="date"
            placeholder="选择日期"
            @change="date1"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
          <span class="to">至</span>
          <el-date-picker
            v-model="value4"
            type="date"
            placeholder="选择日期"
            @change="date2"
            value-format="yyyy-MM-dd"
          ></el-date-picker>
        </div>
        <div>
          <span>检索</span>
          <el-input v-model="value3" placeholder="请输入企业名称"></el-input>
        </div>
        <button @click="search">查询</button>
        <button @click="add">添加第三方企业</button>
      </div>
      <div class="nav-bottom">
        <el-table :data="list" border style="width: 100%">
          <el-table-column prop="bname" label="企业名称"></el-table-column>
          <el-table-column prop="main_account" label="主账号"></el-table-column>
          <el-table-column prop="address" label="地区" width="400px"></el-table-column>
          <el-table-column prop="validity_time" label="服务器到期时间"></el-table-column>
          <el-table-column prop="state" label="状态" :formatter="changeStatus"></el-table-column>
          <el-table-column fixed="right" label="操作">
            <template slot-scope="scope">
              <el-button type="text" size="small" @click="check(scope.row.bid)">查看</el-button>
              <el-button type="text" size="small" @click="update(scope.row.bid)">编辑</el-button>
              <el-button
                type="text"
                size="small"
                v-if="flag==scope.row.state"
                @click="stop = true; repause(scope.row.bid)"
              >暂停</el-button>
              <el-button
                type="text"
                size="small"
                v-else
                @click="startUse = true; reStart(scope.row.bid)"
              >启用</el-button>
              <el-dialog title="启用企业" :visible.sync="startUse" append-to-body width="30%">
                <el-form :model="form2">
                  <el-form-item label="服务到期时间" required>
                    <el-date-picker
                      v-model="form2.dateTime"
                      type="datetime"
                      placeholder="选择日期时间"
                      value-format="yyyy-MM-dd HH:mm:ss"
                    ></el-date-picker>
                  </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                  <el-button @click="startUse = false">取 消</el-button>
                  <el-button type="primary" @click="startUse = false;start();">确 定</el-button>
                </div>
              </el-dialog>
              <el-dialog title="提示" :visible.sync="stop" width="30%" append-to-body>
                <span>确认暂停合作？暂停合作后，该企业下所有账号不可登录服务,确认后执行操作</span>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="stop = false">取 消</el-button>
                  <el-button type="primary" @click="stop = false;pause()">确 定</el-button>
                </span>
              </el-dialog>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          layout="prev, pager, next"
          :total="total"
          :page-size="pageSize"
          :current-page="pageNum"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { regionData, CodeToText, TextToCode } from "element-china-area-data";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("deploy");
export default {
  data() {
    return {
      area: [],
      areaOption: regionData,
      cooperateValue: "",
      cooperate: [
        {
          cooperateValue: "",
          label: "全部"
        },
        {
          cooperateValue: 0,
          label: "合作中"
        },
        {
          cooperateValue: 1,
          label: "暂停合作"
        }
      ],
      options2: [
        {
          value2: "选项1",
          label: "企业名称"
        },
        {
          value2: "选项2",
          label: "主账号"
        }
      ],
      
      value2: "",
      value3: "",
      value4: "",
      startUse: false,
      stop: false,
      flag: false,
      form2: {
        dateTime: ""
      },
      formLabelWidth: "120px",
      coop:""
    };
  },
  mounted() {
    this.deployFormAsync();
  },
  computed: {
    ...mapState([
      "pageNum",
      "pageSize",
      "list",
      "total",
      "thirdBussinessManager"
    ])
  },
  methods: {
    ...mapActions([
      "deployFormAsync",
      "changePageAsync",
      "reStartAsync",
      "pauseAsync",
      "searchAsync"
    ]),
    add() {
      this.$router.push(`/Index/others`);
    },
    check(prop) {
      this.$router.push(`/Index/otherDetails`);
      sessionStorage.setItem("threeDetailID", JSON.stringify(prop));
    },
    update(prop) {
      this.$router.push(`/Index/othersUpdate`);
      sessionStorage.setItem("otherUpdateID", JSON.stringify(prop));
    },
    //合作
    changeStatus(row, column) {
      switch (row.state) {
        case 0:
          return "合作中";
          break;
        case 1:
          return "暂停合作";
          break;
      }
    },
    //启用
    reStart(prop) {
      sessionStorage.setItem("threeID", JSON.stringify(prop));
    },
    async start() {
      let id = JSON.parse(sessionStorage.getItem("threeID"));
      let dateTime = this.form2.dateTime;
      const data = await this.reStartAsync({
        bid: id,
        validityTime: dateTime
      });
      if (data.code === 200) {
        this.flag == true;
        this.$message("重启成功");
        this.deployFormAsync();
      } else if (data.code === 206) {
        this.$message("输入时间不合理,必须为当前时间以后,请重新输入");
      }
    },
    //暂停
    repause(prop) {
      sessionStorage.setItem("threeBID", JSON.stringify(prop));
    },
    async pause() {
      let id = JSON.parse(sessionStorage.getItem("threeBID"));
      const data = await this.pauseAsync({
        bid: id
      });
      if (data.code === 200) {
        this.flag == false;
        this.$message("暂停成功");
        this.deployFormAsync();
      } else if (data.code === 201) {
        this.$message("暂停失败，联系管理员");
      }
    },
    //地区获取
    handleChange(value) {
      let province = CodeToText[value[0]];
      let city = CodeToText[value[1]];
      let area = CodeToText[value[2]];
      this.location = province + city + area;
    },
    //到期时间
    date1(value) {
      this.startTime = value;
    },
    date2(value) {
      this.endTime = value;
    },
    //查询
    search() {
      let location = this.location;
      let start = this.startTime;
      let end = this.endTime;
      let coop = this.cooperateValue;      
      let companyName = this.value3;
      this.$store.commit("deploy/initLocation", location);
      this.$store.commit("deploy/initStart", start);
      this.$store.commit("deploy/initEnd", end);
      this.$store.commit("deploy/initCoop", coop);
      this.$store.commit("deploy/initWriteName", companyName);
      this.searchAsync();
    },
    //分页
    handleCurrentChange(val) {
      this.$store.commit("deploy/changePage", val);
      this.deployFormAsync();
    }
  }
};
</script>

<style scoped>
.deploy {
  width: 96%;
  height: 100%;
  margin: 0 auto;
}
p {
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(192, 188, 188);
}
.nav {
  width: 100%;
  height: 800px;
}
.deploy .nav-top {
  width: 100%;
  height: 100px;
  display: flex;
}
input {
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 150px;
  height: 34px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 20px;
  margin-top: 1px;
}
.deploy .nav-top button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 120px;
  height: 34px;
  text-align: center;
  /* padding: 10px 15px; */
  margin-left: 30px;
  margin-top: 3px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.deploy .nav-top button:hover {
  cursor: pointer;
}
.deploy .nav-top button:nth-of-type(1) {
  width: 80px;
}
.deploy .nav-top > div {
  margin-right: 15px;
}
.deploy .nav-top span {
  margin-right: 15px;
  font-size: 14px;
  font-weight: 500;
}
.nav-bottom {
  width: 96%;
  height: 600px;
  margin: 0 auto;
}
.el-pagination {
  text-align: center;
  margin-top: 15px;
}
.timeBetween .el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 160px;
}
.to {
  margin-left: 15px;
}
</style>

<style >
.deploy .nav-top .el-input {
  width: 150px;
}
.deploy .area .el-input {
  width: 220px;
}
</style>