<template>
  <div class="appContainer">
    <p>app更新设置</p>
    <div class="container">
      <div class="box">
        <span>终端APP更新设置</span>
        <el-button type="text" @click="dialogTerminalVisible = true" class="btn1">发布更新</el-button>
        <el-dialog title="发布更新" :visible.sync="dialogTerminalVisible">
          <el-form :model="formTerminal" ref="formTerminal" :rules="rules">
            <el-form-item label="安装包：" :label-width="formLabelWidth" required>
              <el-upload
                class="upload-demo"
                ref="upload"
                :file-list="fileListTerminal"
                :before-upload="beforeUploadTerminal"
                action=""
                :http-request="uploadFileTerminal"
                multiple
                :limit="1"
                :on-exceed="handleExceed"
              >
                <el-button size="small" type="primary">点击上传</el-button>
                <div slot="tip" class="el-upload__tip">只能上传APK文件</div>
              </el-upload>
            </el-form-item>
            <el-form-item label="客户端版本号：" prop="client_version" :label-width="formLabelWidth">
              <!-- <span>1.1（上传安装包后自动获取）</span> -->
              <el-input v-model="formTerminal.client_version" disabled ></el-input>
            </el-form-item>
            <el-form-item label="版本号：" prop="version" :label-width="formLabelWidth">
              <!-- <span>1（更换安装包后自动续号）</span> -->
              <el-input v-model="formTerminal.version" disabled ></el-input>
            </el-form-item>
            <el-form-item label="更新提示：" prop="description" :label-width="formLabelWidth">
              <el-input type="textarea" v-model="formTerminal.description" placeholder="限500字"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogTerminalVisible = false">取 消</el-button>
            <el-button type="primary" @click="submitTerminal('formTerminal')">确 定</el-button>
          </div>
        </el-dialog>
        <div class="detail">
          <div>
            <i>客户端版本号：</i>
            <span>{{ terminalApp.client_version }}</span>
          </div>
          <div>
            <i>版本号：</i>
            <span>{{ terminalApp.version }}</span>
          </div>
          <div>
            <i>安装包：</i>
            <span>{{ terminalApp.url }}</span>
            <button class="download" >
              <a :href="tdownloadUrl" style="text-decoration:none;color:white">下载</a>
            </button>
          </div>
          <div class="scribe">
            <i>更新提示：</i>
            <div>{{ terminalApp.description }}</div>
          </div>
          <div>
            <i>发布人：</i>
            <span>{{ terminalApp.creator }}</span>
          </div>
          <div>
            <i>发布时间：</i>
            <span>{{ terminalApp.ctime }}</span>
          </div>
        </div>
      </div>
      <div class="box">
        <span>调度员app更新设置</span>
        <el-button type="text" @click="dialogDispatcherVisible = true" class="btn3">发布更新</el-button>
        <el-dialog title="发布更新" :visible.sync="dialogDispatcherVisible">
          <el-form :model="formDispatcher"  ref="formDispatcher" :rules="rules2">
            <el-form-item label="安装包：" :label-width="formLabelWidth" required>
              <el-upload
                class="upload-demo"
                :file-list="fileListDispatcher"
                :before-upload="beforeUploadDispatcher"
                action=""
                :http-request="uploadFileDispatcher"
                multiple
                :limit="1"
                :on-exceed="handleExceed"
              >
                <el-button size="small" type="primary">点击上传</el-button>
                <div slot="tip" class="el-upload__tip">只能上传APK文件</div>
              </el-upload>
            </el-form-item>
            <el-form-item label="客户端版本号：" prop="client_version" :label-width="formLabelWidth">
              <!-- <span>1.1（上传安装包后自动获取）</span> -->
              <el-input v-model="formDispatcher.client_version" disabled></el-input>
            </el-form-item>
            <el-form-item label="版本号：" prop="version" :label-width="formLabelWidth">
              <!-- <span>1（更换安装包后自动续号）</span> -->
              <el-input v-model="formDispatcher.version" disabled></el-input>
            </el-form-item>
            <el-form-item label="更新提示：" prop="description" :label-width="formLabelWidth">
              <el-input type="textarea" v-model="formDispatcher.description" placeholder="限500字"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogDispatcherVisible = false">取 消</el-button>
            <el-button 
              type="primary"
              @click="submitDispatcher('formDispatcher')"
            >确 定</el-button>
          </div>
        </el-dialog>
        <div class="detail">
          <div>
            <i>客户端版本号：</i>
            <span>{{ dispatcherApp.client_version }}</span>
          </div>
          <div>
            <i>版本号：</i>
            <span>{{ dispatcherApp.version }}</span>
          </div>
          <div>
            <i>安装包：</i>
            <span>{{ dispatcherApp.url }}</span>
            <button class="download">
              <a :href="ddownloadUrl" style="text-decoration:none;color:white">下载</a>
            </button>
          </div>
          <div class="scribe">
            <i>更新提示：</i>
            <div>{{ dispatcherApp.description }}</div>
          </div>
          <div>
            <i>发布人：</i>
            <span>{{ dispatcherApp.creator }}</span>
          </div>
          <div>
            <i>发布时间：</i>
            <span>{{ dispatcherApp.ctime }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
import { async } from 'q';
const { mapState, mapActions } = createNamespacedHelpers("appUpdate");
export default {
  data() {
    return {
      dialogTerminalVisible: false,
      dialogDispatcherVisible: false,
      formTerminal: {
        file: '',
        description: '',
        version: 0,
        client_version: 0,
        code: 1 // 终端
      },
      terminalParmas: new FormData(),
      dispatcherParmas: new FormData(),
      formDispatcher: {
        file: '',
        description: '',
        version: 0,
        client_version: 0,
        code: 2 // 调度员
      },
      fileListTerminal: [],
      fileListDispatcher: [],
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      formLabelWidth: "120px",
      rules: {
        description: [
          { required: true, message: '请输入提示语', trigger: 'blur' },
        ],
        version: [
          { required: true, message: '请输入版本', trigger: 'blur' }
        ],
        client_version: [
          { required: true, message: '请输入客户端版本', trigger: 'blur' }
        ],
      },
      rules2: {
        description: [
          { required: true, message: '请输入提示语', trigger: 'blur' },
        ],
        version: [
          { required: true, message: '请输入版本', trigger: 'blur' }
        ],
        client_version: [
          { required: true, message: '请输入客户端版本', trigger: 'blur' }
        ],
      },
      fileList: [
        {
          name: "food.jpeg",
          url:
            "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
        },
        {
          name: "food2.jpeg",
          url:
            "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
        }
      ],
      tdownloadUrl: '',
      ddownloadUrl: ''
    };
  },
  mounted() {
    this.getUrl()
  },
  computed: {
    ...mapState([
      "terminalApp",
      "dispatcherApp"
    ])
  },
  methods: {
    ...mapActions([
      "uploadFileAsync",
      "uploadTerminalAsync",
      "uploadDispatcherAsync",
      "searchAppAsync"
    ]),
    async getUrl() {
      await this.searchAppAsync();
      this.tdownloadUrl = this.$download + this.terminalApp.url;
      this.ddownloadUrl = this.$download + this.dispatcherApp.url;
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制最多选择 1 个文件`
      );
    },
    beforeRemove(file, fileList) {
      return this.$confirm(`确定移除 ${file.name}？`);
    },
    //上传
    // async doUpload(params) {
    //   const file = params.file;
    //   const form = new FormData();
    //   form.append("file", file);
    //   const data = await this.uploadFileAsync(form);
    //   console.log(data);
    // },
     /**
     *  终端
     */
    beforeUploadTerminal(file) {
      // 验证格式
      let index= file.name.lastIndexOf(".");
      let ext = file.name.substr(index + 1);
      let isAPK = false;
      if (ext === 'apk') {
        isAPK = true;
      } else {
        isAPK = false;
      }
      if (!isAPK) {
        this.$message.error('仅支持APK格式!');
      }
      // 验证文件名
      let isName = false;
      let index2= file.name.lastIndexOf(".");
      let name = file.name.substring(0, index);
      let nameArray = name.split('_');
      if (nameArray[1] === 'cli') {
        this.formTerminal.version = nameArray[3];
        this.formTerminal.client_version = nameArray[2];
        isName = true
      } else {
        this.$message.error('请上传终端apk!');
        isName = false;
      }
      let isOk = false;
      if (isName === true && isAPK === true) {
        isOk = true;
      }
      return isOk;
    },
    // 覆盖上传，自定义上传
    uploadFileTerminal(file) {
      this.terminalParmas.append('file',file.file);
    },
    // 提交上传文件
    submitTerminal(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          setTimeout(() => {
            loading.close();
          }, 10000);
          this.dialogTerminalVisible = false
          // console.log(this.formTerminal);
          this.terminalParmas.append('code', this.formTerminal.code);
          this.terminalParmas.append('description', this.formTerminal.description);
          this.terminalParmas.append('version', this.formTerminal.version);
          this.terminalParmas.append('client_version', this.formTerminal.client_version);
          let data = await this.uploadTerminalAsync(this.terminalParmas);
          if (data) {
            this.$message({
              message: '修改成功!',
              type: 'success'
            });
            loading.close();
          } else {
            this.$message.error('修改失败');
            loading.close();
          }
          // this.getUrl()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
      
    },
     /**
     *  调度员
     */
    beforeUploadDispatcher(file) {
      let index= file.name.lastIndexOf(".");
      let ext = file.name.substr(index + 1);
      let isAPK = false;
      if (ext === 'apk') {
        isAPK = true;
      } else {
        isAPK = false;
      }
      if (!isAPK) {
        this.$message.error('仅支持APK格式!');
      }
      // 验证文件名
      let isName = false;
      let index2= file.name.lastIndexOf(".");
      let name = file.name.substring(0, index);
      let nameArray = name.split('_');
      if (nameArray[1] === 'dis') {
        this.formDispatcher.version = nameArray[3];
        this.formDispatcher.client_version = nameArray[2];
        isName = true
      } else {
        this.$message.error('请上传调度员apk!');
        isName = false;
      }
      let isOk = false;
      if (isName === true && isAPK === true) {
        isOk = true;
      }
      return isOk;
    },
    // 覆盖上传，自定义上传
    uploadFileDispatcher(file) {
      this.dispatcherParmas.append('file',file.file);
    },
    // 提交上传文件
    submitDispatcher(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(0, 0, 0, 0.7)'
          });
          setTimeout(() => {
            loading.close();
          }, 10000);
          this.dialogDispatcherVisible = false
          console.log(this.formDispatcher);
          this.dispatcherParmas.append('code', this.formDispatcher.code);
          this.dispatcherParmas.append('description', this.formDispatcher.description);
          this.dispatcherParmas.append('version', this.formDispatcher.version);
          this.dispatcherParmas.append('client_version', this.formDispatcher.client_version);
          let data = await this.uploadDispatcherAsync(this.dispatcherParmas);
          if (data) {
            this.$message({
              message: '修改成功!',
              type: 'success'
            });
            loading.close();
          } else {
            this.$message.error('修改失败');
            loading.close();
          }
        } else {
          console.log('请确认信息填写无误');
          return false;
        }
      });
      
      
      // console.log(data);
    }
  }
};
</script>

<style>
  .appContainer .el-form-item__label {
    width: 130px !important;
  }
  .appContainer .el-form-item__content {
    margin-left: 150px !important;
  }
  .appContainer .el-input.is-disabled .el-input__inner {
    color: black !important;
    /* cursor: auto !important; */
    background-color: #fff !important;
    border: 1px solid #DCDFE6 !important;
  }
  
</style>

<style scoped>
.appContainer {
  width: 96%;
  height: 100%;
  margin: 0 auto;
}
.container {
  width: 100%;
  height: 750px;
  overflow-y: auto;
}
p {
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(192, 188, 188);
}
.box {
  width: 100%;
  height: 260px;
  border-bottom: 1px solid rgb(192, 188, 188);
  margin-bottom: 20px;
}
.box > span {
  font-size: 16px;
}
.box > button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  height: 34px;
  padding: 0;
  text-align: center;
  margin-top: 3px;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
  width: 100px;
}
.btn1 {
  margin-left: 100px;
}
.btn2 {
  margin-left: 70px;
}
.btn3 {
  margin-left: 85px;
}
.box button {
  cursor: pointer;
}
.detail {
  width: 80%;
  height: 190px;
  margin: 20px 0 0 227px;
}
.scribe {
  display: flex;
}

.detail > div {
  margin-bottom: 10px;
}
.box i {
  font-size: 14px;
  font-style: normal;
  display: inline-block;
  width: 160px;
  text-align: left;
}
.scribe > div {
  /* margin-left: 44px; */
}
.download {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  height: 30px;
  padding: 0;
  text-align: center;
  margin-left: 50px;
  margin-top: 3px;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
  width: 50px;
}
</style>