import controlApi from "../services/controlService";

export default {
    namespaced: true,
    state: {
        pageNum1: 1,
        pageSize1: 5,
        bname1:'',
        list1: [],
        total1: 4,
        onlineStatus1:'',
        name1:"",
        username1:"",
        mobile1:"",
        number1:"",
        clientStatus1:'',

        pageNum2: 1,
        pageSize2: 5,
        list2: [],
        total2:4,
        onlineStatus2:"",
        businessName2:"",
        accountStatus2:"",
        name2:"",
        userName2:"",
        mobile2:""
    },
    mutations: {
        //终端数据
        terminalData(state, payload) {
            Object.assign(state, {
                list1: payload.list,
                total1:payload.total,
            })
        },
        //终端分页
        changeTerminalPage(state, payload) {
            Object.assign(state, {
                pageNum1: payload
            })
        },
        //终端查询参数
        initBnameT1(state, payload){
            Object.assign(state, {
                bname1: payload
            })
        },
        initOnlineT1(state, payload){
            Object.assign(state, {
                onlineStatus1: payload
            })
        },
        initClientT1(state, payload){
            Object.assign(state, {
                clientStatus1: payload
            })
        },
        initNameT1(state, payload){
            Object.assign(state, {
                name1: payload
            })
        },
        initUsernameT1(state, payload){
            Object.assign(state, {
                username1: payload
            })
        },
        initMobileT1(state, payload){
            Object.assign(state, {
                mobile1: payload
            })
        },
        initNumberT1(state, payload){
            Object.assign(state, {
                number1: payload
            })
        },
        //调度员列表数据获取
        yardData(state, payload) {
            Object.assign(state, {
                list2: payload.list,
                total2:payload.total,
            })
        },
        //调度员分页
        changeYardPage(state, payload) {
            Object.assign(state, {
                pageNum2: payload
            })
        },
         //调度员查询参数
         initBnameT2(state, payload){
            Object.assign(state, {
                businessName2: payload
            })
        },
        initOnlineStatusT2(state, payload){
            Object.assign(state, {
                onlineStatus2: payload
            })
        },
        initAccountStatus2(state, payload){
            Object.assign(state, {
                accountStatus2: payload
            })
        },
        initNameT2(state, payload){
            Object.assign(state, {
                name2: payload
            })
        },
        initUsernameT2(state, payload){
            Object.assign(state, {
                userName2: payload
            })
        },
        initMobileT2(state, payload){
            Object.assign(state, {
                mobile2: payload
            })
        },
    },
    actions: {
        //终端列表数据获取
        async terminalFormAsync(context) {
            const params = {
                pageSize: context.state.pageSize1,
                pageNum: context.state.pageNum1,
                onlineStatus:context.state.onlineStatus1,
                bname:context.state.bname1,
                clientStatus:context.state.clientStatus1,
                name:context.state.name1,
                username:context.state.username1,
                mobile:context.state.mobile1,
                number:context.state.number1,
            };
            const {data} = await controlApi.terminalForm(params);
            context.commit("terminalData", data)
            return data;
        },
         //调度员列表数据获取
         async yardFormAsync(context) {
            const params = {
                pageSize: context.state.pageSize2,
                pageNum: context.state.pageNum2,
                onlineStatus:context.state.onlineStatus2,
                businessName:context.state.businessName2,
                accountStatus:context.state.accountStatus2,
                name:context.state.name2,
                userName:context.state.userName2,
                mobile:context.state.mobile2,
            };
            const {data} = await controlApi.yardForm(params);
            context.commit("yardData", data)
            return data;
        },
       //终端连接方式更新
       async connectTerminalAsync(context,payload) {
        const data = await controlApi.connectTerminal(payload);
        return data;
    },
    }
}