import messageApi from "../services/messageService";

export default {
    namespaced: true,
    state: {
        pageNum: 1,
        pageSize: 6,
        list: [],
        total: 4,
        allPullMessage: {},
        detalis:{},
        title:""
    },
    mutations: {
        setData(state, payload) {
            //total整合总条数            
            Object.assign(state, {
                list: payload.allPullMessage.list,
                total: payload.AllCount
            })
        },
        changePage(state, payload) {
            Object.assign(state, {
                pageNum: payload
            })
        },
        initTitle(state, payload){
            Object.assign(state, {
                title: payload
            })
        },
        //官方消息管理查询-通过标题查询
        searchData(state, payload) {       
            Object.assign(state, {
                list: payload.pullMessageList.list,
                total: payload.pullMessageList.total
            })
        },
        //官方推送消息详情查看
        detailData(state, payload){
            Object.assign(state, {
                detalis:payload
            })
        }
    },
    actions: {
        //列表数据获取
        async messageFormAsync(context, payload) {
            const params={
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum
            }
            const {
                data
            } = await messageApi.messageForm(params);
            context.commit("setData", data)
            return data;
        },
        //官方消息管理查询-通过标题查询
        async searchAsync(context) {
            const params={
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum,
                title: context.state.title,
            }
            const {data} = await messageApi.search(params);            
            context.commit("searchData", data);
        },
        //官方推送消息详情查看
        async detailAsync(context, payload) {
            const {data} = await messageApi.detail(payload);            
            context.commit("detailData", data);
            return data;
        },
        //删除推送消息
        async deletAsync(context, payload) {
            const data = await messageApi.delet(payload);
            return data;            
        },
        //添加发布官方消息
        async addAsync(context, payload) {
            const data = await messageApi.add(payload);
            return data;            
        },
        //修改推送消息
        async updateAsync(context, payload) {
            const data = await messageApi.update(payload);
            return data;            
        },
    }
}