<template>
  <div class="control">
    <p>远程控制</p>
    <div class="box1">
      <span>下载app日志</span>
      <i>提示：该操作仅对在线终端或在线调度员app有效，如果不在线，操作将失败</i>
      <div class="parts">
        <i>app端：</i>
        <span @click="func('A');" v-bind:class="{'active':(active=='A') }">
          <el-radio v-model="radio" label="1">终端</el-radio>
        </span>
        <span @click="func('B');" v-bind:class="{'active':(active=='B') }">
          <el-radio v-model="radio" label="2">调度员</el-radio>
        </span>
        <!-- 终端 -->
        <div class="block block1" v-if="block=='A'">
          <div class="terminal">
            <i>账号：</i>
            <input type="text" placeholder="请输入终端账号" v-model="terAcount" />
            <button @click="dialogTableVisible = true ;terminals()">查询</button>
            <el-dialog title="查询终端账号" :visible.sync="dialogTableVisible" width="81%">
              <div class="queryContainer">
                <div class="nav-top">
                  <div>
                    <span>企业</span>
                    <input type="text" v-model="value1" placeholder="请输入企业名称" />
                  </div>
                  <div>
                    <span>在线状态</span>
                    <el-select v-model="value2" placeholder="请选择">
                      <el-option
                        :options="options2"
                        v-for="item in options2"
                        :key="item.value2"
                        :label="item.label"
                        :value="item.value2"
                      ></el-option>
                    </el-select>
                  </div>
                  <div>
                    <span>账号状态</span>
                    <el-select v-model="value3" placeholder="请选择">
                      <el-option
                        :options="options3"
                        v-for="item in options3"
                        :key="item.value3"
                        :label="item.label"
                        :value="item.value3"
                      ></el-option>
                    </el-select>
                  </div>
                  <div>
                    <span>检索</span>
                    <el-select v-model="value4" placeholder="请选择">
                      <el-option
                        :options="options4"
                        v-for="item in options4"
                        :key="item.value4"
                        :label="item.label"
                        :value="item.value4"
                      ></el-option>
                    </el-select>
                  </div>
                  <input type="text" v-model="terminalQuery" placeholder="请输入查询内容" />
                  <button class="search" @click="terminalSearch">查询</button>
                </div>
                <el-table :data="list1" border @selection-change="terminalSelect">
                  <el-table-column type="selection" width="55" property="bid"></el-table-column>
                  <el-table-column property="bname" label="企业名称"></el-table-column>
                  <el-table-column property="onlineStatus" label="在线状态" :formatter="changeOnline1"></el-table-column>
                  <el-table-column property="name" label="姓名"></el-table-column>
                  <el-table-column property="username" label="账号"></el-table-column>
                  <el-table-column property="mobile" label="电话"></el-table-column>
                  <el-table-column property="workType" label="工种"></el-table-column>
                  <el-table-column property="number" label="绑定设备编号"></el-table-column>
                  <el-table-column property="clientStatus" label="账号状态" :formatter="changeClient1"></el-table-column>
                </el-table>
                <el-pagination
                  background
                  layout="prev, pager, next"
                  :total="total1"
                  :page-size="pageSize1"
                  :current-page="pageNum1"
                  @current-change="terminalPage"
                ></el-pagination>
              </div>
              <span slot="footer" class="dialog-footer">
                <el-button @click="dialogTableVisible = false">取 消</el-button>
                <el-button type="primary" @click="dialogTableVisible = false;terminalSure()">确 定</el-button>
              </span>
            </el-dialog>
          </div>
          <button class="download" @click="terminalDown()">下载日志</button>
        </div>
        <!-- 调度员 -->
        <div class="block block1" v-if="block=='B'">
          <div class="yardman">
            <i>账号：</i>
            <input type="text" placeholder="请输入调度员账号" v-model="yAcount" />
            <button @click="visible = true;yards()">查询</button>
            <el-dialog title="查询调度员账号" :visible.sync="visible" append-to-body width="81%">
              <div class="yardContainer">
                <div class="nav-top">
                  <div>
                    <span>企业</span>
                    <input type="text" v-model="value5" />
                  </div>
                  <div>
                    <span>APP在线状态</span>
                    <el-select v-model="value6" placeholder="请选择">
                      <el-option
                        :options="options6"
                        v-for="item in options6"
                        :key="item.value6"
                        :label="item.label"
                        :value="item.value6"
                      ></el-option>
                    </el-select>
                  </div>
                  <div>
                    <span>账号状态</span>
                    <el-select v-model="value7" placeholder="请选择">
                      <el-option
                        :options="options7"
                        v-for="item in options7"
                        :key="item.value7"
                        :label="item.label"
                        :value="item.value7"
                      ></el-option>
                    </el-select>
                  </div>
                  <div>
                    <span>检索</span>
                    <el-select v-model="value8" placeholder="请选择">
                      <el-option
                        :options="options8"
                        v-for="item in options8"
                        :key="item.value8"
                        :label="item.label"
                        :value="item.value8"
                      ></el-option>
                    </el-select>
                  </div>
                  <input type="text" v-model="yardQuery" />
                  <button class="search" @click="yardSearch">查询</button>
                </div>
                <el-table :data="list2" border @selection-change="yardSelect">
                  <el-table-column type="selection" width="55" property="bid"></el-table-column>
                  <el-table-column property="businessName" label="企业名称"></el-table-column>
                  <el-table-column
                    property="onlineStatus"
                    label="APP在线状态"
                    :formatter="changeOnline2"
                  ></el-table-column>
                  <el-table-column property="name" label="姓名"></el-table-column>
                  <el-table-column property="userName" label="账号"></el-table-column>
                  <el-table-column property="mobile" label="电话"></el-table-column>
                  <el-table-column property="accountStatus" label="账号状态" :formatter="changeClient2"></el-table-column>
                </el-table>
                <el-pagination
                  background
                  layout="prev, pager, next"
                  :total="total2"
                  :page-size="pageSize2"
                  :current-page="pageNum2"
                  @current-change="yardPage"
                ></el-pagination>
              </div>
              <span slot="footer" class="dialog-footer">
                <el-button @click="visible = false">取 消</el-button>
                <el-button type="primary" @click="visible = false;yardSure()">确 定</el-button>
              </span>
            </el-dialog>
          </div>
          <button class="download" @click="yardmanDown()">下载日志</button>
        </div>
      </div>
    </div>
    <div class="box2">
      <span>终端连接方式更新</span>
      <i>提示：该操作仅对在线终端app有效，如果不在线，操作将失败</i>
      <div class="parts2">
        <i>连接方式：</i>
        <span @click="second('C');" v-bind:class="{'point':(point=='C') }">
          <el-radio v-model="radio2" label="1">IP连接</el-radio>
        </span>
        <span @click="second('D');" v-bind:class="{'point':(point=='D') }">
          <el-radio v-model="radio2" label="2">域名连接</el-radio>
        </span>
        <div class="way way1" v-if="way=='C'">
          <div class="yardman">
            <i>IP地址：</i>
            <input type="text" placeholder="请输入IP地址" v-model="writeIp" />
          </div>
          <div class="yardman">
            <i>端口号：</i>
            <input type="text" placeholder="请输入端口号" v-model="writePort" />
          </div>
          <button @click="connectIP">同步更新</button>
        </div>
        <div class="way way1" v-if="way=='D'">
          <div class="yardman">
            <i>域名：</i>
            <input type="text" placeholder="请输入域名" v-model="writeDomain" />
          </div>
          <button @click="connectName">同步更新</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
import FileSaver from "file-saver";
import XLSX from "xlsx";
import qs from "qs";
const { mapState, mapActions } = createNamespacedHelpers("control");
export default {
  data() {
    return {
      radio: "1",
      active: "A",
      block: "A",
      radio2: "1",
      point: "C",
      way: "C",
      dialogTableVisible: false,
      dialogVisible: false,
      visible: false,
      options2: [
        {
          value2: "",
          label: "全部"
        },
        {
          value2: 1,
          label: "在线"
        },
        {
          value2: 0,
          label: "离线"
        }
      ],
      options3: [
        {
          value3: "",
          label: "全部"
        },
        {
          value3: 1,
          label: "启用"
        },
        {
          value3: 0,
          label: "禁用"
        }
      ],
      options4: [
        {
          value4: 0,
          label: "姓名"
        },
        {
          value4: 1,
          label: "账号"
        },
        {
          value4: 2,
          label: "电话"
        },
        {
          value4: 3,
          label: "设备编号"
        }
      ],
      value1: "",
      value2: "",
      value3: "",
      value4: "",
      value5: "",
      value6: "",
      value7: "",
      value8: "",
      terminalQuery: "",
      yardQuery: "",
      options6: [
        {
          value6: "",
          label: "全部"
        },
        {
          value6: 1,
          label: "在线"
        },
        {
          value6: 0,
          label: "离线"
        }
      ],
      options7: [
        {
          value7: "",
          label: "全部"
        },
        {
          value7: 1,
          label: "启用"
        },
        {
          value7: 0,
          label: "禁用"
        }
      ],
      options8: [
        {
          value8: 0,
          label: "姓名"
        },
        {
          value8: 1,
          label: "账号"
        },
        {
          value8: 2,
          label: "电话"
        }
      ],
      terAcount: "",
      yAcount: "",
      writeIp: "",
      writePort: "",
      writeDomain: ""
    };
  },
  mounted() {},
  computed: {
    ...mapState([
      "pageNum1",
      "pageSize1",
      "list1",
      "total1",
      "pageNum2",
      "pageSize2",
      "list2",
      "total2"
    ])
  },
  methods: {
    ...mapActions([
      "terminalFormAsync",
      "yardFormAsync",
      "connectTerminalAsync"
    ]),
    func: function(b) {
      this.active = b;
      this.block = b;
    },
    second: function(c) {
      this.point = c;
      this.way = c;
    },
    //终端获取表单
    terminals() {
      this.terminalFormAsync();
    },
    //终端搜索查询
    terminalSearch() {
      this.bnameT1 = this.value1;
      // console.log(this.bname1);
      this.onlineStatusT1 = this.value2;
      // console.log(this.value2);
      this.clientStatusT1 = this.value3;
      console.log(this.clientStatus1);
      if (this.value4 == 0) {
        this.nameT1 = this.terminalQuery;
        this.usernameT1 = "";
        this.mobileT1 = "";
        this.numberT1 = "";
      } else if (this.value4 == 1) {
        this.nameT1 = "";
        this.usernameT1 = this.terminalQuery;
        this.mobileT1 = "";
        this.numberT1 = "";
      } else if (this.value4 == 2) {
        this.nameT1 = "";
        this.usernameT1 = "";
        this.mobileT1 = this.terminalQuery;
        this.numberT1 = "";
      } else if (this.value4 == 3) {
        this.nameT1 = "";
        this.usernameT1 = "";
        this.mobileT1 = "";
        this.numberT1 = this.terminalQuery;
      }
      this.$store.commit("control/initBnameT1", this.bnameT1);
      this.$store.commit("control/initOnlineT1", this.onlineStatusT1);
      this.$store.commit("control/initClientT1", this.clientStatusT1);
      this.$store.commit("control/initNameT1", this.nameT1);
      this.$store.commit("control/initUsernameT1", this.usernameT1);
      this.$store.commit("control/initMobileT1", this.mobileT1);
      this.$store.commit("control/initNumberT1", this.numberT1);
      this.terminalFormAsync();
    },
    changeOnline1(row, column) {
      switch (row.onlineStatus) {
        case 0:
          return "离线";
          break;
        case 1:
          return "在线";
          break;
        case 2:
          return "在线";
          break;
        case 3:
          return "在线";
          break;
      }
    },
    changeClient1(row, column) {
      switch (row.clientStatus) {
        case 0:
          return "禁用";
          break;
        case 1:
          return "启用";
          break;
      }
    },
    //终端分页
    terminalPage(val) {
      this.$store.commit("control/changeTerminalPage", val);
      this.terminalFormAsync();
    },

    //调度员表单获取
    yards() {
      this.yardFormAsync();
    },
    //调度员搜索查询
    yardSearch() {
      let bnameT2 = this.value5;
      let onlineStatusT2 = this.value6;
      let accountStatus2 = this.value7;
      if (this.value8 == 0) {
        this.nameT2 = this.yardQuery;
        this.userNameT2 = "";
        this.mobileT2 = "";
      } else if (this.value8 == 1) {
        this.nameT2 = "";
        this.userNameT2 = this.yardQuery;
        this.mobileT2 = "";
      } else if (this.value8 == 2) {
        this.nameT2 = "";
        this.userNameT2 = "";
        this.mobileT2 = this.yardQuery;
      }
      console.log(this.nameT2);
      console.log(this.userNameT2);
      console.log(this.mobileT2);

      this.$store.commit("control/initBnameT2", bnameT2);
      this.$store.commit("control/initOnlineStatusT2", onlineStatusT2);
      this.$store.commit("control/initAccountStatus2", accountStatus2);
      this.$store.commit("control/initNameT2", this.nameT2);
      this.$store.commit("control/initUsernameT2", this.userNameT2);
      this.$store.commit("control/initMobileT2", this.mobileT2);
      this.yardFormAsync();
    },
    //调度员分页
    yardPage(val) {
      this.$store.commit("control/changeYardPage", val);
      this.yardFormAsync();
    },
    changeOnline2(row, column) {
      switch (row.onlineStatus) {
        case 0:
          return "离线";
          break;
        case 1:
          return "在线";
          break;
        case 2:
          return "在线";
          break;
        case 3:
          return "在线";
          break;
      }
    },
    changeClient2(row, column) {
      switch (row.accountStatus) {
        case 0:
          return "禁用";
          break;
        case 1:
          return "启用";
          break;
      }
    },
    //下载日志
    terminalSelect(val) {
      if (val.length == 1) {
        this.terminalAccount = val[0].username;
        this.terminalID = val[0].bid;
      } else if (val.length > 1) {
        this.$message("只能选择一个账号，请重新选择");
        this.terminalFormAsync();
      }
    },
    terminalSure() {
      this.terAcount = this.terminalAccount;
      this.terminalFormAsync();
    },
    async terminalDown() {
      // let terminalID = this.terminalID;
      // let terIsData = {
      //   bid: terminalID,
      //   userName: this.terminalAccount
      // };
      console.log("下载数据id与名字", this.terminalID, this.terminalAccount);
      console.log("默认地址", this.$download);
      if (this.terminalID == undefined) {
        this.$message("请选择终端账号");
      } else {
        let ter = {
          bid: this.terminalID,
          userName: this.terminalAccount
        };

        const { data: res } = await this.$http.get(
          "/remote/handleExportBugLogsByBid",
          { params: ter },
          { responseType: "blob" } // 指定获取数据的类型为blob
        );
        if (res.code !== 200) {
          return this.$message.error(res.data);
        }
        if (res.code == 200) {
          let url = window.URL.createObjectURL(new Blob([res.data]));
          let link = document.createElement("a");
          link.style.display = "none";
          link.href = url;
          link.setAttribute("download", this.terminalAccount + ".txt");
          document.body.appendChild(link);
          link.click();
        }
      }
    },
    yardSelect(val) {
      if (val.length == 1) {
        this.yardAccount = val[0].businessName;
        this.yardID = val[0].bid;
      } else if (val.length > 1) {
        this.$message("只能选择一个账号，请重新选择");
        this.yardFormAsync();
      }
    },
    yardSure() {
      this.yAcount = this.yardAccount;
    },
    async yardmanDown() {
      if (this.yardID == undefined) {
        this.$message("请选择调度员账号");
      } else {
        let ter = {
          bid: this.yardID,
          userName: this.yardAccount
        };
        const { data: res } = await this.$http.get(
          "/remote/handleExportBugLogsByBid",
          { params: ter },
          { responseType: "blob" } // 指定获取数据的类型为blob
        );
        if (res.code !== 200) {
          return this.$message.error(res.data);
        }
        if (res.code == 200) {
          // 将获取到的二进制数据通过方法创建个路径地址
          let url = window.URL.createObjectURL(new Blob([res.data]));
          let link = document.createElement("a");
          link.style.display = "none";
          link.href = url;
          link.setAttribute("download", this.yardAccount + ".txt");
          document.body.appendChild(link);
          link.click();
        }
      }
    },
    //同步更新
    async connectIP() {
      let radio2 = this.radio2;
      let writeIp = this.writeIp;
      let writePort = this.writePort;
      let writeDomain = this.writeDomain;
      const data = await this.connectTerminalAsync({
        option: radio2,
        ip: writeIp,
        port: writePort,
        domainName: writeDomain
      });
      if (data.code == 200) {
        this.$message("切换成功");
      } else {
        this.$message("切换失败，请联系管理员");
      }
    },
    async connectName() {
      let radio2 = this.radio2;
      let writeIp = this.writeIp;
      let writePort = this.writePort;
      let writeDomain = this.writeDomain;
      const data = await this.connectTerminalAsync({
        option: radio2,
        ip: writeIp,
        port: writePort,
        domainName: writeDomain
      });
      if (data.code == 200) {
        this.$message("切换成功");
      } else {
        this.$message("切换失败，请联系管理员");
      }
    }
  }
};
</script>

<style scoped>
.control {
  width: 96%;
  height: 100%;
  margin: 0 auto;
}
.control > p {
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(192, 188, 188);
}
.parts {
  width: 91%;
  height: 200px;
  border-bottom: 1px solid;
  margin-top: 20px;
  margin-left: 140px;
}
.parts > i {
  margin-right: 40px;
}
.parts > span {
  display: inline-block;
  width: 50px;
  height: 20px;
  margin-right: 80px;
}
.block {
  width: 100%;
  height: 80px;
  /* border: 1px solid #ddd; */
  margin-top: 30px;
}
.terminal {
  margin-top: 20px;
  margin-bottom: 30px;
}
.terminal > input {
  -web-kit-appearance: none;
  -moz-appearance: none;
  margin-left: 50px;
  width: 300px;
  height: 30px;
  padding-left: 20px;
}
.yardman {
  margin-bottom: 30px;
}
.yardman > input {
  -web-kit-appearance: none;
  -moz-appearance: none;
  margin-left: 50px;
  width: 300px;
  height: 30px;
  padding-left: 20px;
}
button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  text-align: center;
  border-radius: 5px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.terminal > button {
  width: 70px;
  height: 34px;
  margin-left: 30px;
}
.yardman > button {
  width: 70px;
  height: 34px;
  margin-left: 30px;
}
button:hover {
  cursor: pointer;
}
.download {
  width: 120px;
  height: 40px;
}
.parts > button {
  width: 100px;
  height: 34px;
}
i {
  font-style: normal;
}
.box1,
.box2 {
  font-size: 14px;
}
.box1 {
  margin-bottom: 40px;
}
.box1 > i {
  margin-left: 60px;
  color: rgba(153, 153, 153, 1);
}
.box2 > i {
  margin-left: 30px;
  color: rgba(153, 153, 153, 1);
}
.way {
  width: 100%;
  height: 140px;
  /* border: 1px solid #ddd; */
  margin-top: 30px;
}
.parts2 {
  width: 91%;
  height: 240px;
  border-bottom: 1px solid;
  margin-top: 20px;
  margin-left: 140px;
}
.parts2 > i {
  margin-right: 40px;
}
.parts2 > span {
  display: inline-block;
  width: 50px;
  height: 20px;
  margin-right: 80px;
}
.way > button {
  width: 100px;
  height: 34px;
}
.queryContainer {
  width: 96%;
}

.nav-top {
  width: 100%;
  height: 100px;
  display: flex;
}
.nav-top input {
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 150px;
  height: 34px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 20px;
  margin-top: 1px;
}
.search {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 120px;
  height: 34px;
  text-align: center;
  margin-left: 30px;
  margin-top: 3px;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
  width: 80px;
}

.nav-top > div {
  margin-right: 15px;
}
.nav-top span {
  margin-right: 15px;
  font-size: 14px;
