<template>
  <div class="addContainer">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Index/company' }">企业管理</el-breadcrumb-item>
      <el-breadcrumb-item>添加企业</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="form">
      <el-form
        :model="ruleForm"
        ref="ruleForm"
        label-width="220px"
        class="demo-ruleForm"
        :rules="rules"
      >
        <div class="first">
          <el-form-item label="企业名称" prop="bname">
            <el-input v-model="ruleForm.bname" placeholder="限20字"></el-input>
          </el-form-item>
          <el-form-item label="主账号" prop="mainAccount" >
            <el-input v-model="ruleForm.mainAccount" @input="ifRepeat" placeholder="3-10位数字、字母、字符"></el-input>
          </el-form-item>
          <p class="ptips">主账号添加后不可修改，且不可重复，初始密码与账号相同</p>
          <el-form-item label="企业地址" prop="address">
            <label>
              <el-cascader
                v-model="ruleForm.area"
                :options="areaOption"
                @change="handleChange"
                clearable
              ></el-cascader>
            </label>
            <el-input
              v-model="ruleForm.address"
              placeholder="请输入详细地址"
              @change="adress"
              id="suggestId"
            ></el-input>
          </el-form-item>
          <el-form-item style="marginTop:-15px;">
            <baidu-map
              :center="center"
              :zoom="zoom"
              @ready="handler"
              class="baiduMap"
              :scroll-wheel-zoom="true"
              id="l-map"
            >
              <!-- <bm-view class="map"></bm-view> -->
              <bm-local-search :keyword="arealocation" :auto-viewport="true" :panel="false"></bm-local-search>
              <bm-local-search
                :keyword="ruleForm.address"
                :auto-viewport="true"
                :panel="false"
                @searchcomplete="getGps"
              ></bm-local-search>
            </baidu-map>
          </el-form-item>
          <el-form-item label="负责人" prop="principal">
            <el-input v-model="ruleForm.principal" placeholder="限20字"></el-input>
          </el-form-item>
          <el-form-item label="负责人电话" prop="phone">
            <el-input v-model="ruleForm.phone"></el-input>
          </el-form-item>
          <el-form-item label="服务到期时间" prop="dateTime">
            <el-date-picker
              v-model="ruleForm.dateTime"
              type="date"
              placeholder="选择日期"
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="合作状态" prop="status">
            <el-select v-model="ruleForm.status" placeholder="请选择合作状态" @change="sta">
              <el-option label="合作中" value="0"></el-option>
              <el-option label="暂停合作" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="其他说明" prop="others">
            <el-input type="textarea" v-model="ruleForm.others" placeholder="限500字" maxlength="500"></el-input>
          </el-form-item>
          <el-form-item label="终端账号数限制" prop="limit">
            <el-input v-model="ruleForm.limit" ></el-input>
          </el-form-item>
        </div>
        <div class="second">
          <el-form-item label="功能配置" required>
            <div class="checkGroup">
              <el-checkbox label="语音通话功能" v-model="ruleForm.voice" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="视频通话功能" v-model="ruleForm.see" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="对讲功能" v-model="ruleForm.talk" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="电子围栏功能" v-model="ruleForm.rail" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="轨迹功能" v-model="ruleForm.track" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="广播功能" v-model="ruleForm.broad" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="无声侦护功能" v-model="ruleForm.detect" true-label="0" false-label="1"></el-checkbox>
            </div>
          </el-form-item>
          <el-form-item label="终端视频录制时长限制" prop="time" required>
            <el-radio-group v-model="ruleForm.time" @change="finalTime">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="无限制"></el-radio>
              <el-radio label="限制"></el-radio>
            </el-radio-group>
            <el-input placeholder="200" :disabled="one" v-model="ruleForm.timeLimit"></el-input>
            <span>分钟</span>
            <i>（到达该时长自动结束录制，最大不可超过1440分钟）</i>
          </el-form-item>
          <el-form-item label="终端视频自动上传限制" prop="upload" required>
            <el-radio-group v-model="ruleForm.upload" @change="videoUpload">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="无限制"></el-radio>
              <el-radio label="限制"></el-radio>
            </el-radio-group>
            <el-input placeholder="1000" :disabled="two" v-model="ruleForm.videoLimit"></el-input>
            <span>MB</span>
            <i>（大于该值的视频不自动上传）</i>
          </el-form-item>
          <el-form-item label="调度员app视频自动上传限制" prop="app" required>
            <el-radio-group v-model="ruleForm.app" @change="appUpload">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="无限制"></el-radio>
              <el-radio label="限制"></el-radio>
            </el-radio-group>
            <el-input placeholder="1000" :disabled="three" v-model="ruleForm.appLimit"></el-input>
            <span>MB</span>
            <i>（在非wifi网络下大于该值的视频不自动上传，wifi网络下不受限制）</i>
          </el-form-item>
          <el-form-item label="终端日志记录开关" prop="switch" required>
            <el-radio-group v-model="ruleForm.switch" @change="logSwitch">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="关"></el-radio>
              <el-radio label="开"></el-radio>
            </el-radio-group>
            <el-input placeholder="5" :disabled="four" v-model="ruleForm.switchLimit"></el-input>
            <span>天自动清理</span>
            <i>（如设置5天，则每5天清空日志；设置为0时不自动清理）</i>
          </el-form-item>
          <el-form-item label="调度员app日志记录开关" prop="log" required>
            <el-radio-group v-model="ruleForm.log" @change="appLog">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="关"></el-radio>
              <el-radio label="开"></el-radio>
            </el-radio-group>
            <el-input placeholder="5" :disabled="five" v-model="ruleForm.appLogLimit"></el-input>
            <span>天自动清理</span>
            <i>（如设置5天，则每5天清空日志；设置为0时不自动清理）</i>
          </el-form-item>
          <el-form-item label="企业存储空间报警配置" prop="save" required>
            <el-radio-group v-model="ruleForm.save" @change="savation">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="自定义"></el-radio>
            </el-radio-group>
            <el-input placeholder="500" :disabled="six" v-model="ruleForm.saveLimit"></el-input>
            <span>GB</span>
            <i>（企业存储空间大于该值则会触发一次报警通知）</i>
          </el-form-item>
        </div>
        <el-form-item>
          <el-button type="primary" @click="add">保存</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { regionData, CodeToText, TextToCode } from "element-china-area-data";
import { BmlMarkerClusterer } from "vue-baidu-map";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("company");
export default {
  data() {
    return {
      ruleForm: {
        bname: "",
        mainAccount: "",
        area: "",
        address: "",
        principal: "",
        phone: "",
        status: "",
        others: "",
        limit: "",
        dateTime: "",
        voice: 1,
        see: 1,
        talk: 1,
        rail: 1,
        track: 1,
        broad: 1,
        detect: 1,
        time: "",
        timeLimit: 60,
        upload: "",
        videoLimit: 1000,
        app: "",
        appLimit: 500,
        switch: "",
        switchLimit: 20,
        log: "",
        appLogLimit: 20,
        save: "",
        saveLimit: 100
      },
      areaOption: regionData,
      one: true,
      two: true,
      three: true,
      four: true,
      five: true,
      six: true,
      center: { lng: 104, lat: 30 },
      zoom: 13,
      location: "北京",
      keyword: "百度",
      arealocation: "",
      rules: {
        bname: [
          { required: true, message: "请输企业名称", trigger: "blur" },
          { min: 1, max: 20, message: "长度不能超过20个字符", trigger: "blur" }
        ],
        mainAccount: [
          { required: true, message: "请输入主账号", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "3-10位数字、字母、字符",
            trigger: "blur"
          }
        ],
        area: [
          { required: true, message: "请选择省市区", trigger: "blur" }
        ],
        address: [
          { required: true, message: "请输入详细地址", trigger: "blur" }
        ],
        principal: [
          { required: true, message: "请输入负责人", trigger: "blur" }
        ],
        phone: [
          { required: true, message: "请输入负责人电话", trigger: "blur" }
        ],
        dateTime: [
          { required: true, message: "请选择服务到期时间", trigger: "blur" }
        ],
        status: [
          { required: true, message: "请选择合作状态", trigger: "blur" }
        ],
        others: [{ max: 500, message: "限500字", trigger: "blur" }],
        limit: [
          { required: true, message: "请输入终端账号数限制", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {},
  methods: {
    ...mapActions(["additionAsync", "formAsync","judgementAsync"]),
    handleChange(value) {
      let province = CodeToText[value[0]];
      let city = CodeToText[value[1]];
      let area = CodeToText[value[2]];
      this.arealocation = province + city + area;
      this.site = value;
      // console.log(province);
      this.ruleForm.address = this.arealocation;
      // console.log(this.ruleForm.address);
    },
    adress() {
      // console.log(this.ruleForm.address);
    },
    //合作状态
    sta() {
      this.staus = this.ruleForm.status;
    },
    //终端视频录制时长限制
    finalTime() {
      if (this.ruleForm.time == "跟随系统设置") {
        this.videoTime = 0;
        this.ruleForm.timeLimit = "";
        this.one = true;
      } else if (this.ruleForm.time == "无限制") {
        this.videoTime = 1;
        this.ruleForm.timeLimit = "";
        this.one = true;
      } else if (this.ruleForm.time == "限制") {
        this.videoTime = 2;
        this.one = false;
      }
    },
    //终端视频自动上传限制
    videoUpload() {
      if (this.ruleForm.upload == "跟随系统设置") {
        this.uploadVideo = 0;
        this.ruleForm.videoLimit = "";
        this.two = true;
      } else if (this.ruleForm.upload == "无限制") {
        this.uploadVideo = 1;
        this.ruleForm.videoLimit = "";
        this.two = true;
      } else if (this.ruleForm.upload == "限制") {
        this.uploadVideo = 2;
        this.two = false;
      }
    },
    //调度员app视频自动上传限制
    appUpload() {
      if (this.ruleForm.app == "跟随系统设置") {
        this.appVideo = 0;
        this.ruleForm.appLimit = "";
        this.three = true;
      } else if (this.ruleForm.app == "无限制") {
        this.appVideo = 1;
        this.ruleForm.appLimit = "";
        this.three = true;
      } else if (this.ruleForm.app == "限制") {
        this.appVideo = 2;
        this.three = false;
      }
    },
    //终端日志记录开关
    logSwitch() {
      if (this.ruleForm.switch == "跟随系统设置") {
        this.switchLog = 0;
        this.ruleForm.switchLimit = "";
        this.four = true;
      } else if (this.ruleForm.switch == "关") {
        this.switchLog = 1;
        this.ruleForm.switchLimit = "";
        this.four = true;
      } else if (this.ruleForm.switch == "开") {
        this.switchLog = 2;
        this.four = false;
      }
    },
    //调度员app日志记录开关
    appLog() {
      if (this.ruleForm.log == "跟随系统设置") {
        this.logApp = 0;
        this.ruleForm.appLogLimit = "";
        this.five = true;
      } else if (this.ruleForm.log == "关") {
        this.logApp = 1;
        this.ruleForm.appLogLimit = "";
        this.five = true;
      } else if (this.ruleForm.log == "开") {
        this.logApp = 2;
        this.five = false;
      }
    },
    //企业存储空间报警配置
    savation() {
      if (this.ruleForm.save == "跟随系统设置") {
        this.saveSet = 0;
        this.ruleForm.saveLimit = "";
        this.six = true;
      } else if (this.ruleForm.save == "自定义") {
        this.saveSet = 1;
        this.six = false;
      }
    },
    //判断账号是否重复
    async ifRepeat(){
      let currentAccount=this.ruleForm.mainAccount;
      const data=await this.judgementAsync({
        mainAccount:currentAccount
      })
      if (data.code==203) {
        this.$message("主账号重复，请重新输入");
        this.ruleForm.mainAccount='';
      }
    },
    //添加企业
    async add() {
      let name = this.ruleForm.bname;
      let acount = this.ruleForm.mainAccount.toLowerCase();
      let addrDetail = this.ruleForm.address;
      let charge = this.ruleForm.principal;
      let phone = this.ruleForm.phone;
      let dateTime = this.ruleForm.dateTime + " " + "00:00:00";
      let coStatus = this.staus;
      let others = this.ruleForm.others;
      let limit = this.ruleForm.limit;
      let voice = this.ruleForm.voice;
      let see = this.ruleForm.see;
      let talk = this.ruleForm.talk;
      let rail = this.ruleForm.rail;
      let track = this.ruleForm.track;
      let broad = this.ruleForm.broad;
      let detect = this.ruleForm.detect;
      let time = this.videoTime;
      let timeLimit = this.ruleForm.timeLimit;
      let uploadVideo = this.uploadVideo;
      let videoLimit = this.ruleForm.videoLimit;
      let appVideo = this.appVideo;
      let appLimit = this.ruleForm.appLimit;
      let switchLog = this.switchLog;
      let switchLimit = this.ruleForm.switchLimit;
      let logApp = this.logApp;
      let appLogLimit = this.ruleForm.appLogLimit;
      let saveSet = this.saveSet;
      let saveLimit = this.ruleForm.saveLimit;
      let lat = this.gpsLocation.lat;
      let lon = this.gpsLocation.lng;

      let form = {
        bname: name,
        mainAccount: acount,
        address: addrDetail,
        personInCharge: charge,
        picPhone: phone,
        validityTime: dateTime,
        status: coStatus,
        description: others,
        deviceNumLimit: limit,
        isCall: voice,
        vedio: see,
        talkback: talk,
        mapRange: rail,
        trail: track,
        broadcast: broad,
        listener: detect,
        vedioTimeLimitSys: time,
        vedioTimeLimit: timeLimit,
        isAutoUploadSys: uploadVideo,
        isAutoUpload: videoLimit,
        dispatcherUploadLimitSys: appVideo,
        dispatcherUploadLimit: appLimit,
        appLogSys: switchLog,
        appLog: switchLimit,
        dispatcherLogSys: logApp,
        dispatcherLog: appLogLimit,
        storeTipSys: saveSet,
        storeTip: saveLimit,
        longitude: lon,
        latitude: lat
      };
      const data = await this.additionAsync(form);
      if (data.code == 200) {
        this.$message("添加成功");
        this.formAsync();
        sessionStorage.setItem("address", JSON.stringify(this.ruleForm.area));
        this.$router.push(`/Index/company`);
      } else if (data.code == 201) {
        this.$message("添加失败，红点标记处为必填项，请按要求填写");
      } else if (data.code == 202) {
        this.$message("用户名已经存在");
      } else if (data.code == 203) {
        this.$message("主账号已经存在");
      } else if (data.code == 204) {
        this.$message("主账号格式错误");
      } else if (data.code == 501) {
        this.$message("电话号码格式错误");
      } else if (data.code == 502) {
        this.$message("向企业后台输入数据失败");
      } else if (data.code == 211) {
        this.$message("添加失败，请先设置系统默认设置");
        this.$router.push(`/Index/otherSet`);
      } else {
        this.$message("红点标记处为必填项，请按要求填写");
      }
    },

    //百度地图
    // handler({ BMap, map }) {
    //   // 百度地图API功能
