import axios from "axios";
import qs from "qs";

export default{
    //字典--查询所有
    async dictionaryForm(params) {                                    
        let {data} = await axios.get("/system/handleSelectAllDictionary", {params});            
        return data;
    },
    //字典--启用和禁用
    async reStart(params) {                        
        let {data} = await axios.post("/system/handleForbidDic", qs.stringify(params));
        return data;
    },
    //新增
    async addition(params) {                        
        let {data} = await axios.post("/system/handleAddDictionary", qs.stringify(params));
        return data;
    },
    //获取单条
    async detail(params) {                        
        let {data} = await axios.get("/system/handleSelectDicByDid", {params});
        return data;
    },
    //修改
    async update(params) {                        
        let {data} = await axios.post("/system/handleUpdateDic", qs.stringify(params));
        return data;
    },
}