<template>
  <div class="otherDetails">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Index/deploy' }">独立部署授权管理</el-breadcrumb-item>
      <el-breadcrumb-item>第三方企业明细</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="details">
      <div label="企业信息" name="first" class="first">
        <h3>基本信息</h3>
        <div>
          <i>企业名称：</i>
          <span>{{thirdbusinessmanager.bname}}</span>
        </div>
        <div>
          <i>企业账号：</i>
          <span>{{thirdbusinessmanager.mainAccount}}</span>
        </div>
        <div>
          <i>企业地址：</i>
          <span>{{thirdbusinessmanager.address}}</span>
        </div>
        <div>
          <i>负责人：</i>
          <span>{{thirdbusinessmanager.personInCharge}}</span>
        </div>
        <div>
          <i>负责人电话：</i>
          <span>{{thirdbusinessmanager.picPhone}}</span>
        </div>
        <div>
          <i>服务到期时间：</i>
          <span>{{thirdbusinessmanager.validityTime}}</span>
        </div>
        <div>
          <i>合作状态：</i>
          <span id="status"></span>
        </div>
        <div>
          <i class="otherD">其它说明：</i>
          <div class="description">{{thirdbusinessmanager.description}}</div>
        </div>
        <div>
          <i>终端账号数限制：</i>
          <span>{{thirdbusinessmanager.deviceNumLimit}}</span>
        </div>
        <div>
          <i>功能配置：</i>
          <span id="fun1"></span>
          <span id="fun2"></span>
          <span id="fun3"></span>
          <span id="fun4"></span>
          <span id="fun5"></span>
          <span id="fun6"></span>
          <span id="fun7"></span>
        </div>
        <div>
          <i>终端视频自动上传限制：</i>
          <span id="video"></span>
        </div>
         <div>
          <i>调度员app视频自动上传限制：</i>
          <span id="auto"></span>
        </div>
         <div>
          <i>终端日志记录开关：</i>
          <span id="terminal"></span>
        </div>
         <div>
          <i>调度员app日志记录开关：</i>
          <span id="applog"></span>
        </div>
         <div>
          <i>企业存储空间报警配置：</i>
          <span id="store"></span>
        </div>
        <div>
          <i>绑定服务器IP：</i>
          <span>{{thirdbusinessmanager.serverHost}}</span>
        </div>
        <div>
          <i>绑定服务器MAC地址：</i>
          <span>{{thirdbusinessmanager.serverMac}}</span>
        </div>
        <h3>其他信息</h3>
        <div>
          <i>创建时间：</i>
          <span>{{thirdbusinessmanager.ctime}}</span>
        </div>
        <div>
          <i>更新时间：</i>
          <span>{{thirdbusinessmanager.utime}}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("deploy");
export default {
  data() {
    return {
      diySetting: {},
      thirdbusinessmanager: {}
    };
  },
  async mounted() {
    let id = JSON.parse(sessionStorage.getItem("threeDetailID"));
    const data = await this.thirdDetailAsync({
      bid: id
    });
    console.log(data);
    this.diySetting = data.diySetting;
    this.thirdbusinessmanager = data.thirdbusinessmanager;
    let status = document.getElementById("status");
    if (this.thirdbusinessmanager.state == 0) {
      status.innerHTML = "合作中";
    } else if (this.thirdbusinessmanager.state == 1) {
      status.innerHTML = "暂停合作";
    };
    //功能
    let fun1 = document.getElementById("fun1");
    if (this.diySetting.isCall == 0) {
      fun1.style.display = "block";
      fun1.innerHTML = "语音通话功能";
      fun1.style.marginRight = "20px";
    } else if (this.diySetting.isCall == 1) {
      fun1.style.display = "none";
      fun1.style.marginRight = "0px";
    }
    let fun2 = document.getElementById("fun2");
    if (this.diySetting.vedio == 0) {
      fun2.style.display = "block";
      fun2.innerHTML = "视频通话功能";
      fun2.style.marginRight = "20px";
    } else if (this.diySetting.vedio == 1) {
      fun2.style.display = "none";
      fun2.style.marginRight = "0px";
    }
    let fun3 = document.getElementById("fun3");
    if (this.diySetting.talkback == 0) {
      fun3.style.display = "block";
      fun3.innerHTML = "对讲功能";
      fun3.style.marginRight = "20px";
    } else if (this.diySetting.talkback == 1) {
      fun3.style.display = "none";
      fun3.style.marginRight = "0px";
    }
    let fun4 = document.getElementById("fun4");
    if (this.diySetting.mapRange == 0) {
      fun4.style.display = "block";
      fun4.innerHTML = "电子围栏功能";
      fun4.style.marginRight = "20px";
    } else if (this.diySetting.mapRange == 1) {
      fun4.style.display = "none";
      fun4.style.marginRight = "0px";
    }
    let fun5 = document.getElementById("fun5");
    if (this.diySetting.trail == 0) {
      fun5.style.display = "block";
      fun5.innerHTML = "轨迹功能";
      fun5.style.marginRight = "20px";
    } else if (this.diySetting.trail == 1) {
      fun5.style.display = "none";
      fun5.style.marginRight = "0px";
    }
    let fun6 = document.getElementById("fun6");
    if (this.diySetting.broadcast == 0) {
      fun6.style.display = "block";
      fun6.innerHTML = "广播功能";
      fun6.style.marginRight = "20px";
    } else if (this.diySetting.broadcast == 1) {
      fun6.style.display = "none";
      fun6.style.marginRight = "0px";
    }
    let fun7 = document.getElementById("fun7");
    if (this.diySetting.listener == 0) {
      fun7.style.display = "block";
      fun7.innerHTML = "无声侦护功能";
      fun7.style.marginRight = "20px";
    } else if (this.diySetting.listener == 1) {
      fun7.style.display = "none";
      fun7.style.marginRight = "0px";
    }
    //终端视频自动上传限制
    let video = document.getElementById("video");
    if (this.diySetting.vedioTimeLimitSys == 0) {
      video.innerHTML = "跟随系统配置";
      video.style.marginRight = "20px";
    } else if (this.diySetting.vedioTimeLimitSys == 1) {
      video.innerHTML = "不限制";
    } else if (this.diySetting.vedioTimeLimitSys == 2) {
      video.innerHTML = "限制" + this.diySetting.vedioTimeLimit + "MB";
    }

    //调度员app视频自动上传限制
    let auto = document.getElementById("auto");
    if (this.diySetting.isAutoUploadSys == 0) {
      auto.innerHTML = "跟随系统配置";
      auto.style.marginRight = "20px";
    } else if (this.diySetting.isAutoUploadSys == 1) {
      auto.innerHTML = "不限制";
    } else if (this.diySetting.isAutoUploadSys == 2) {
      auto.innerHTML = "限制" + this.diySetting.isAutoUpload + "MB";
    }
    //终端日志记录开关
    let terminal = document.getElementById("terminal");
    if (this.diySetting.appLogSys == 0) {
      terminal.innerHTML = "跟随系统配置";
      terminal.style.marginRight = "20px";
    } else if (this.diySetting.appLogSys == 1) {
      terminal.innerHTML = "关";
    } else if (this.diySetting.appLogSys == 2) {
      terminal.innerHTML = "开";
    }
    //调度员app日志记录开关
    let applog = document.getElementById("applog");
    if (this.diySetting.dispatcherLogSys == 0) {
      applog.innerHTML = "跟随系统配置";
      applog.style.marginRight = "20px";
    } else if (this.diySetting.dispatcherLogSys == 1) {
      applog.innerHTML = "关";
    } else if (this.diySetting.dispatcherLogSys == 2) {
      applog.innerHTML = "开";
    }
    //企业存储空间报警配置
    let store = document.getElementById("store");
    if (this.diySetting.storeTipSys == 0) {
      store.innerHTML = "跟随系统配置";
      store.style.marginRight = "20px";
    } else if (this.diySetting.storeTipSys == 1) {
      store.innerHTML = this.diySetting.storeTip + "GB";
    }
  },
  computed: {
    ...mapState(["details"])
  },
  methods: {
    ...mapActions(["thirdDetailAsync"])
  }
};
</script>

<style scoped>
.otherDetails {
  width: 96%;
  height: 900px;
  margin: 0 auto;
}
.first {
  width: 100%;
  height: 800px;
  overflow-y: auto;
}
.first div {
  margin-bottom: 20px;
  font-size: 14px;
  display: flex;
}
.first i {
  font-style: normal;
  display: inline-block;
  width: 200px;
  margin-right: 40px;
}
.description {
  width: 1200px;
  height: 140px;
  margin: 0;
  padding: 0;
}

.otherD {
  display: inline-block;
  width: 200px;
  height: 140px;
}
</style>

<style>
.otherDetails .el-breadcrumb {
  text-align: center;
  line-height: 30px;
  padding-top: 10px;
  border-bottom: 1px solid rgb(192, 188, 188);
}
.otherDetails .el-breadcrumb__inner a,
.el-breadcrumb__inner.is-link {
  font-size: 20px;
}
.otherDetails .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  font-size: 16px;
}
</style>