import axios from "axios";
import qs from "qs";

export default {
    //终端查询
    async terminalForm(params) {
        let {data} = await axios.post("/remote/handleQueryClientByCondition", qs.stringify(params));
        return data;
    },
    //调度员查询
    async yardForm(params) {
        let {data} = await axios.post("/remote/handleSelectDispatcherByCondition", qs.stringify(params));
        return data;
    },
    //终端连接方式更新
    async connectTerminal(params) {
        let {data} = await axios.post("/remote/handleUpdateClientConnectionMethod", qs.stringify(params));
        return data;
    },
}