<template>
  <div class="message">
    <p class="name">官方消息管理</p>
    <div class="nav">
      <div class="nav-top">
        <div>
          <span>检索</span>
          <input type="text" placeholder="输入标题查询" v-model="title" />
          <button class="query" @click="query">查询</button>
        </div>
        <el-button type="text" @click="dialogFormVisible = true" class="news">发布官方消息</el-button>
        <el-dialog title="发布官方消息" :visible.sync="dialogFormVisible">
          <el-form :model="form"  :rules="rules">
            <el-form-item label="推送时间" prop="time" :label-width="formLabelWidth" >
              <el-radio-group v-model="form.time" @change="radioSelect">
                <el-radio label="创建后既发布"></el-radio>
                <el-radio label="定时推送"></el-radio>
                <div class="time">
                  <span>设定推送时间</span>
                  <el-date-picker
                    v-model="form.dateTime"
                    type="datetime"
                    placeholder="选择日期时间"
                    format='yyyy-MM-dd HH:mm'
                    value-format="yyyy-MM-dd HH"
                    :disabled="timeShow"
                  ></el-date-picker>
                </div>
              </el-radio-group>
            </el-form-item>
            <p>提示：发布后不可编辑，只可删除，设定的推送时间只能精确到小时</p>
            <el-form-item label="标题" prop="title" :label-width="formLabelWidth" >
              <el-input v-model="form.title" placeholder="限20字" class="box1"></el-input>
            </el-form-item>
            <el-form-item label="内容" prop="content" :label-width="formLabelWidth" >
              <el-input type="textarea" v-model="form.content" placeholder="限500字" class="box1"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogFormVisible = false;ensureAdd()">确 定</el-button>
          </div>
        </el-dialog>
      </div>
      <div class="nav-bottom">
        <el-table :data="list" border style="width: 100%">
          <el-table-column prop="title" label="标题" width="200px"></el-table-column>
          <el-table-column prop="flag" label="状态" :formatter="changeStatus"></el-table-column>
          <el-table-column prop="ptime" label="推送时间" width="200px"></el-table-column>
          <el-table-column prop="creator" label="创建人"></el-table-column>
          <el-table-column prop="ctime" label="创建时间" width="200px"></el-table-column>
          <el-table-column prop="update_person" label="更新人"></el-table-column>
          <el-table-column prop="utime" label="更新时间" width="200px"></el-table-column>
          <el-table-column fixed="right" label="操作">
            <template slot-scope="scope">
              <el-button
                size="small"
                type="text"
                @click="dialog  = true ;check(scope.row.pid)"
                class="dialog"
              >查看</el-button>
              <el-dialog title="查看官方消息" :visible.sync="dialog   " append-to-body>
                <div class="content">
                  <div class="dia">
                    <div class="dia1">推送时间：</div>
                    <span>{{detalis.ptime}}</span>
                  </div>
                  <div class="dia">
                    <div class="dia1">标题：</div>
                    <span>{{detalis.title}}</span>
                  </div>
                  <div class="dia">
                    <div class="dia1">内容：</div>
                    <span>{{detalis.content}}</span>
                  </div>
                </div>
              </el-dialog>
              <el-button
                type="text"
                size="small"
                v-if="show==scope.row.flag"
                @click="updateDialog = true;update(scope.row.pid)"
              >编辑</el-button>
              <el-button type="text" size="small" @click="delet = true; deleted(scope.row.pid)">删除</el-button>
              <el-dialog title="提示" :visible.sync="delet" width="30%" append-to-body>
                <span>确认删除？</span>
                <span slot="footer" class="dialog-footer">
                  <el-button @click="delet = false">取 消</el-button>
                  <el-button type="primary" @click="delet = false;sure()">确 定</el-button>
                </span>
              </el-dialog>

              <el-dialog title="编辑官方消息" :visible.sync="updateDialog" append-to-body>
                <div class="messageForm2">
                  <el-form :model="form2" :rules="updateRules">
                    <el-form-item label="推送时间" prop="time" :label-width="formLabelWidth" >
                      <el-radio-group v-model="form2.time" @change="updateSelect">
                        <el-radio label="创建后既发布"></el-radio>
                        <el-radio label="定时推送"></el-radio>
                        <div class="time">
                          <span>设定推送时间</span>
                          <el-date-picker
                            v-model="form2.dateTime"
                            type="datetime"
                            placeholder="选择日期时间"
                            format='yyyy-MM-dd HH:mm'
                    value-format="yyyy-MM-dd HH"
                            :disabled="updateShow"
                          ></el-date-picker>
                        </div>
                      </el-radio-group>
                    </el-form-item>
                    <p>提示：发布后不可编辑，只可删除，设定的推送时间只能精确到小时</p>

                    <el-form-item label="标题" prop="title" :label-width="formLabelWidth" >
                      <el-input v-model="form2.title" placeholder="限20字" class="box1"></el-input>
                    </el-form-item>
                    <el-form-item label="内容" prop="content" :label-width="formLabelWidth" >
                      <el-input
                        type="textarea"
                        v-model="form2.content"
                        placeholder="限500字"
                        class="box1"
                      ></el-input>
                    </el-form-item>
                  </el-form>
                </div>
                <div slot="footer" class="dialog-footer">
                  <el-button @click="updateDialog = false">取 消</el-button>
                  <el-button type="primary" @click="updateDialog = false;updateSure()">确 定</el-button>
                </div>
              </el-dialog>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          background
          layout="prev, pager, next"
          :total="total"
          :page-size="pageSize"
          :current-page="pageNum"
          @current-change="handleCurrentChange"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("message");
export default {
  data() {
    return {
      dialogTableVisible: false,
      dialogFormVisible: false,
      form: {
        title: "",
        dateTime: "",
        time: "",
        content: ""
      },
      formLabelWidth: "120px",
      width: "220px",
      dialog: false,
      show: false,
      title: "",
      delet: false,
      timeShow: true,
      updateDialog: false,
      form2: {
        title: "",
        dateTime: "",
        time: "",
        content: ""
      },
      updateShow: true,
      rules:{
        time: [
          { required: true, message: "请选择时间", trigger: "blur" },
        ],
        title: [
          { required: true, message: "请输入标题", trigger: "blur" },
          { min: 1, max: 20, message: "长度不能超过20个字符", trigger: "blur" }
        ],
        content: [
          { required: true, message: "请输入内容", trigger: "blur" },
          { min: 1, max: 500, message: "长度不能超过500个字符", trigger: "blur" }
        ],
      },
      updateRules:{
        time: [
          { required: true, message: "请选择时间", trigger: "blur" },
        ],
        title: [
          { required: true, message: "请输入标题", trigger: "blur" },
          { min: 1, max: 20, message: "长度不能超过20个字符", trigger: "blur" }
        ],
        content: [
          { required: true, message: "请输入内容", trigger: "blur" },
          { min: 1, max: 500, message: "长度不能超过500个字符", trigger: "blur" }
        ],
      }
    };
  },
  mounted() {
    this.messageFormAsync();
  },
  computed: {
    ...mapState(["pageNum", "pageSize", "list", "total", "detalis"])
  },
  methods: {
    ...mapActions([
      "messageFormAsync",
      "changePageAsync",
      "searchAsync",
      "detailAsync",
      "deletAsync",
      "addAsync",
      "updateAsync"
    ]),
    handleChange(value) {
      console.log(value);
    },
    //分页
    handleCurrentChange(val) {
     this.$store.commit("message/changePage", val);
      this.messageFormAsync();
    },
    changeStatus(row, column) {
      switch (row.flag) {
        case 0:
          return "排队中";
          break;
        case 1:
          return "已发布";
          break;
      }
    },
    //查询
     query() {
      let tit = this.title;
      this.$store.commit("message/initTitle", tit);
      this.searchAsync();
    },
    //查看
    check(prop) {
      this.detailAsync({
        pid: prop
      });
    },
    //删除
    deleted(prop) {
      this.id = prop;
    },
    async sure() {
      let id = this.id;
      const data = await this.deletAsync({
        pid: id
      });
      if (data.code == 200) {
        this.$message("删除成功");
        this.messageFormAsync();
      } else if (data.code == 205) {
        this.$message("删除失败，请联系管理员");
      }
    },
    //添加
    radioSelect() {
      if (this.form.time == "创建后既发布") {
        this.flag = 1;
        this.timeShow = true;
        this.form.dateTime = "";
      } else if (this.form.time == "定时推送") {
        this.flag = 0;
        this.timeShow = false;
      }
    },
    async ensureAdd() {
      let state = this.flag;
      let name = this.form.title;
      let time = this.form.dateTime;
      console.log(this.form.dateTime);
      
      let contexts = this.form.content;
      const data = await this.addAsync({
        title: name,
        content: contexts,
        ptime: time,
        flag: state
      });
      if (data.code == 200) {
        this.$message("添加成功");
        this.messageFormAsync();
        this.form.title="";
        this.form.content="";
        this.form.time="";
      } else if (data.code == 207) {
        this.$message("输入数据错误 联系管理员");
      }
    },
    //修改
    async update(prop) {
      this.rowID = prop;
      const data = await this.detailAsync({ pid: prop });
      this.form2.title = data.title;
      this.form2.content = data.content;
      if (data.flag == 0) {
        this.form2.time = "定时推送";
        this.updateShow = false;
        this.form2.dateTime = data.ptime;
      } else if (data.flag == 1) {
        this.form2.time = "创建后既发布";
        this.updateShow = true;
        this.form2.dateTime = "";
      }
    },
    updateSelect() {
      if (this.form2.time == "创建后既发布") {
        this.updateFlag = 1;
        this.updateShow = true;
        this.form2.dateTime = "";
      } else if (this.form2.time == "定时推送") {
        this.updateFlag = 0;
        this.updateShow = false;
      }
    },
    async updateSure() {
      let rowID = this.rowID;
      let name = this.form2.title;
      let time = this.form2.dateTime;
      let contexts = this.form2.content;
      let updateFlag=this.updateFlag;
     const data=await this.updateAsync({
        pid:rowID,
        title:name,
        content:contexts,
        ptime:time,
        flag:updateFlag
      });
      if (data.code == 200) {
        this.$message("修改成功");
        this.messageFormAsync();
      } else if (data.code == 205) {
        this.$message("更新数据异常联系管理员");
      }else if (data.code == 208) {
        this.$message("消息已经推出不可编辑");
      }
      
    }
  }
};
</script>

<style scoped>
.message {
  width: 96%;
  height: 100%;
  margin: 0 auto;
}
.name {
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(192, 188, 188);
}
.nav {
  width: 100%;
  height: 800px;
}
.nav-top {
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: space-between;
}
input {
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 150px;
  height: 34px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 20px;
  margin-top: 1px;
}
.query,
.news {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  height: 34px;
  padding: 0;
  text-align: center;
  margin-left: 30px;
  margin-top: 3px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.query {
  width: 80px;
}
.news {
  width: 120px;
}
.nav-top button:hover {
  cursor: pointer;
}

.nav-top > div {
  margin-right: 15px;
}
.nav-top span {
  margin-right: 15px;
  font-size: 14px;
  font-weight: 500;
}
.nav-bottom {
  width: 96%;
  height: 600px;
  margin: 0 auto;
}
.el-pagination {
  text-align: center;
  margin-top: 15px;
}

.message .time {
  margin-left: 300px;
  margin-top: -28px;
  display: flex;
  width: 400px;
  align-items: center;
}
.message .el-form p {
  font-size: 14px;
  border: none;
  margin-left: 120px;
  color: rgba(153, 153, 153, 1);
}
.el-dialog__wrapper .content {
  width: 100%;
}
.el-dialog__wrapper .content div {
  margin-bottom: 10px;
}
.el-dialog__wrapper .dia {
  width: 100%;
  display: flex;
}
.el-dialog__wrapper .dia1 {
  margin-right: 30px;
}
.el-dialog__wrapper span {
  width: 400px;
}
</style>

<style >
.message .el-form .el-radio-group {
  margin-top: 14px;
}
.message .el-form .el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 1000px;
}
.message .el-form .box1 {
  width: 600px;
}
.nav-bottom .el-table .dialog {
  margin-right: 10px;
}
.message .el-col-8 {
  width: 1400px;
}
.messageForm2 .el-form p {
  font-size: 14px;
  border: none;
  margin-left: 120px;
  color: rgba(153, 153, 153, 1);
}
.messageForm2 .el-form .el-radio-group {
  display: flex;
  align-items: center;
}
.messageForm2 .el-form .box1 {
  width: 550px;
}
.messageForm2 .el-table {
    font-size: 14px;
}
</style>