import axios from "axios";
import qs from "qs";

export default{
        //查询所以数据并分页
        async messageForm(params) {            
            let {data} = await axios.post("/pullMessage/handleAllPullMessage", qs.stringify(params));              
            return data;
        },
        //官方消息管理查询-通过标题查询
        async search(params) {               
            let {data} = await axios.post("/pullMessage/handleQueryPullMessageByTitle", qs.stringify(params));                          
            return data;
        },
        //官方推送消息详情查看
        async detail(params) {               
            let {data} = await axios.post("/pullMessage/handleQueryPullMessageByPid", qs.stringify(params));                          
            return data;
        },
        //删除推送消息
        async delet(params) {               
            let {data} = await axios.post("/pullMessage/handleDeletePullMessage", qs.stringify(params));                          
            return data;
        },
        //添加发布官方消息
        async add(params) {               
            let {data} = await axios.post("/pullMessage/handleAddPullMessage", qs.stringify(params));                          
            return data;
        },
        //修改推送消息
        async update(params) {               
            let {data} = await axios.post("/pullMessage/handleEditPullMessage", qs.stringify(params));                          
            return data;
        },
    }