<template>
  <div class="detailContainer">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Index/company' }">企业管理</el-breadcrumb-item>
      <el-breadcrumb-item>企业明细</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="details">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="企业信息" name="first" class="first">
          <h3>基本信息</h3>
          <div>
            <i>企业名称：</i>
            <span>{{businessManager.bname}}</span>
            <a :href="companyUrl" @click="jump" class="btn" target="_blank">>></a>
          </div>
          <div>
            <i>主账号：</i>
            <span>{{businessManager.mainAccount}}</span>
          </div>
          <div>
            <i>企业地址：</i>
            <span>{{businessManager.address}}</span>
          </div>
          <div>
            <i>负责人：</i>
            <span>{{businessManager.personInCharge}}</span>
          </div>
          <div>
            <i>负责人电话：</i>
            <span>{{businessManager.picPhone}}</span>
          </div>
          <div>
            <i>服务到期时间：</i>
            <span>{{businessManager.validityTime}}</span>
          </div>
          <div>
            <i>合作状态：</i>
            <span id="status"></span>
          </div>
          <div class="otherDiv">
            <i class="otherD">其它说明：</i>
            <div class="description">{{businessManager.description}}</div>
          </div>
          <div>
            <i>终端账号数限制：</i>
            <span>{{businessManager.deviceNumLimit}}</span>
          </div>
          <div>
            <i>功能配置：</i>
            <span id="fun1"></span>
            <span id="fun2"></span>
            <span id="fun3"></span>
            <span id="fun4"></span>
            <span id="fun5"></span>
            <span id="fun6"></span>
            <span id="fun7"></span>
          </div>
          <div>
            <i>终端视频自动上传限制：</i>
            <span id="video"></span>
          </div>
          <div>
            <i>调度员app视频自动上传限制：</i>
            <span id="auto"></span>
          </div>
          <div>
            <i>终端日志记录开关：</i>
            <span id="terminal"></span>
          </div>
          <div>
            <i>调度员app日志记录开关：</i>
            <span id="applog"></span>
          </div>
          <div>
            <i>企业存储空间报警配置：</i>
            <span id="store"></span>
          </div>
          <h3>其他信息</h3>
          <div>
            <i>创建时间：</i>
            <span>{{businessManager.ctime}}</span>
          </div>
          <div>
            <i>更新时间：</i>
            <span>{{businessManager.utime}}</span>
          </div>
        </el-tab-pane>
        <el-tab-pane label="企业统计" name="second" class="second">
          <div>
            <span class="demonstration">地区</span>
            <el-cascader v-model="areaValue" :options="areaOption" @change="location"></el-cascader>
            <button @click="query">查询</button>
          </div>
          <div class="box0">
            <p class="title">项目统计</p>
            <div class="box1">
              <div>
                <p class="count">{{queryData.proCount}}</p>
                <p class="num">项目数</p>
              </div>
              <div>
                <p class="count">{{queryData.proOnBuildCount}}</p>
                <p class="num">进行中</p>
              </div>
              <div>
                <p class="count">{{queryData.proOffBuildCount}}</p>
                <p class="num">已结束</p>
              </div>
            </div>
          </div>
          <div class="box0">
            <p class="title">终端统计</p>
            <div class="box1">
              <div>
                <p class="count">{{queryData.cliCount}}</p>
                <p class="num">终端账号数</p>
              </div>
              <div>
                <p class="count">{{queryData.cliOnLineCount}}</p>
                <p class="num">在线数</p>
              </div>
              <div>
                <p class="count">{{queryData.cliOffLineCount}}</p>
                <p class="num">离线数</p>
              </div>
            </div>
          </div>
          <div class="box0">
            <p class="title">调度员统计</p>
            <div class="box1">
              <div>
                <p class="count">{{queryData.disCount}}</p>
                <p class="num">调度员账号数</p>
              </div>
              <div>
                <p class="count">{{queryData.disOnLineCount}}</p>
                <p class="num">在线数</p>
              </div>
              <div>
                <p class="count">{{queryData.disOffLineCount}}</p>
                <p class="num">离线数</p>
              </div>
            </div>
          </div>
          <div></div>
          <div></div>
        </el-tab-pane>
        <el-tab-pane label="企业空间管理" name="third" class="third">
          <div class="thirdBox">
            <div>
              <p class="p1">已用存储空间</p>
              <p class="use">{{storageUsed}}M</p>
              <button class="clear" @click="dialogFormVisible = true">清除数据</button>
              <el-dialog title="清除数据" :visible.sync="dialogFormVisible">
                <span class="tips">提示：删除后不可恢复，请谨慎操作</span>
                <el-form :model="form">
                  <el-form-item label="选择清除的数据项" :label-width="formLabelWidth" required>
                    <el-checkbox-group v-model="checkType" true-label="number">
                      <el-checkbox label="1">照片</el-checkbox>
                      <el-checkbox label="2">视频</el-checkbox>
                      <el-checkbox label="3">日志</el-checkbox>
                      <el-checkbox label="4">对讲记录</el-checkbox>
                      <el-checkbox label="5">电话录音</el-checkbox>
                      <el-checkbox label="6">广播记录语音</el-checkbox>
                    </el-checkbox-group>
                  </el-form-item>
                  <el-form-item label="选择清除的时间段" :label-width="formLabelWidth" required>
                    <el-select v-model="clearValue" placeholder="请选择时间段">
                      <el-option
                        :options="clear"
                        v-for="item in clear"
                        :key="item.clearValue"
                        :label="item.label"
                        :value="item.clearValue"
                      ></el-option>
                    </el-select>
                  </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                  <el-button @click="dialogFormVisible = false">取 消</el-button>
                  <el-button type="primary" @click="dialogFormVisible = false;sureClear()">确 定</el-button>
                </div>
              </el-dialog>
            </div>
            <div>
              <p class="use">{{storageImages}}M</p>
              <p class="type">照片</p>
            </div>
            <div>
              <p class="use">{{storageVideo}}M</p>
              <p class="type">视频</p>
            </div>
            <div>
              <p class="use">{{storageLog}}M</p>
              <p class="type">日志</p>
            </div>
            <div>
              <p class="use">{{storageTalkBack}}M</p>
              <p class="type">对讲记录</p>
            </div>
            <div>
              <p class="use">{{storagePhoneRecord}}M</p>
              <p class="type">电话录音</p>
            </div>
            <div>
              <p class="use">{{storageBroadcast}}M</p>
              <p class="type">广播记录语音</p>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
import {
  regionDataPlus,
  CodeToText,
  TextToCode
} from "element-china-area-data";
const { mapState, mapActions } = createNamespacedHelpers("company");
export default {
  data() {
    return {
      activeName: "first",
      areaValue: [],
      areaOption: regionDataPlus,
      dialogFormVisible: false,
      form: {},
      formLabelWidth: "200px",
      clear: [
        {
          clearValue: 1,
          label: "1天前"
        },
        {
          clearValue: 7,
          label: "7天前"
        },
        {
          clearValue: 30,
          label: "30天前"
        },
        {
          clearValue: 90,
          label: "90天前"
        },
        {
          clearValue: 180,
          label: "180天前"
        },
        {
          clearValue: 365,
          label: "1年前"
        }
      ],
      clearValue: "",
      checkType: [],
      storageTalkBack: "",
      storageBroadcast: "",
      storageImages: "",
      storageLog: "",
      storageVideo: "",
      storagePhoneRecord: "",
      storageUsed: "",
      companyUrl: "",
      companydata: 666,
      companyid: ""
    };
  },
  async mounted() {
    let id = JSON.parse(sessionStorage.getItem("company")).bid;
    const data = await this.companyDetailAsync(id);
    this.clearAccount = data.businessManager.mainAccount;
    this.storageBid = data.businessManager.bid;
    //存did和id
    sessionStorage.setItem("didUpdate", data.diySettingMapper.did);
    sessionStorage.setItem("idUpdate", data.businessManager.id);
    //合作状态
    let status = document.getElementById("status");
    if (this.businessManager.status == 0) {
      status.innerHTML = "合作中";
    } else if (this.businessManager.status == 1) {
      status.innerHTML = "暂停合作";
    }
    //功能
    let fun1 = document.getElementById("fun1");
    if (this.diySettingMapper.isCall == 0) {
      fun1.style.display = "block";
      fun1.innerHTML = "语音通话功能";
      fun1.style.marginRight = "20px";
    } else if (this.diySettingMapper.isCall == 1) {
      fun1.style.display = "none";
      fun1.style.marginRight = "0px";
    }
    let fun2 = document.getElementById("fun2");
    if (this.diySettingMapper.vedio == 0) {
      fun2.style.display = "block";
      fun2.innerHTML = "视频通话功能";
      fun2.style.marginRight = "20px";
    } else if (this.diySettingMapper.vedio == 1) {
      fun2.style.display = "none";
      fun2.style.marginRight = "0px";
    }
    let fun3 = document.getElementById("fun3");
    if (this.diySettingMapper.talkback == 0) {
      fun3.style.display = "block";
      fun3.innerHTML = "对讲功能";
      fun3.style.marginRight = "20px";
    } else if (this.diySettingMapper.talkback == 1) {
      fun3.style.display = "none";
      fun3.style.marginRight = "0px";
    }
    let fun4 = document.getElementById("fun4");
    if (this.diySettingMapper.mapRange == 0) {
      fun4.style.display = "block";
      fun4.innerHTML = "电子围栏功能";
      fun4.style.marginRight = "20px";
    } else if (this.diySettingMapper.mapRange == 1) {
      fun4.style.display = "none";
      fun4.style.marginRight = "0px";
    }
    let fun5 = document.getElementById("fun5");
    if (this.diySettingMapper.trail == 0) {
      fun5.style.display = "block";
      fun5.innerHTML = "轨迹功能";
      fun5.style.marginRight = "20px";
    } else if (this.diySettingMapper.trail == 1) {
      fun5.style.display = "none";
      fun5.style.marginRight = "0px";
    }
    let fun6 = document.getElementById("fun6");
    if (this.diySettingMapper.broadcast == 0) {
      fun6.style.display = "block";
      fun6.innerHTML = "广播功能";
      fun6.style.marginRight = "20px";
    } else if (this.diySettingMapper.broadcast == 1) {
      fun6.style.display = "none";
      fun6.style.marginRight = "0px";
    }
    let fun7 = document.getElementById("fun7");
    if (this.diySettingMapper.listener == 0) {
      fun7.style.display = "block";
      fun7.innerHTML = "无声侦护功能";
      fun7.style.marginRight = "20px";
    } else if (this.diySettingMapper.listener == 1) {
      fun7.style.display = "none";
      fun7.style.marginRight = "0px";
    }
    //终端视频自动上传限制
    let video = document.getElementById("video");
    if (this.diySettingMapper.vedioTimeLimitSys == 0) {
      video.innerHTML = "跟随系统配置";
      video.style.marginRight = "20px";
    } else if (this.diySettingMapper.vedioTimeLimitSys == 1) {
      video.innerHTML = "不限制";
    } else if (this.diySettingMapper.vedioTimeLimitSys == 2) {
      video.innerHTML = "限制" + this.diySettingMapper.vedioTimeLimit + "MB";
    }

    //调度员app视频自动上传限制
    let auto = document.getElementById("auto");
    if (this.diySettingMapper.isAutoUploadSys == 0) {
      auto.innerHTML = "跟随系统配置";
      auto.style.marginRight = "20px";
    } else if (this.diySettingMapper.isAutoUploadSys == 1) {
      auto.innerHTML = "不限制";
    } else if (this.diySettingMapper.isAutoUploadSys == 2) {
      auto.innerHTML = "限制" + this.diySettingMapper.isAutoUpload + "MB";
    }
    //终端日志记录开关
    let terminal = document.getElementById("terminal");
    if (this.diySettingMapper.appLogSys == 0) {
      terminal.innerHTML = "跟随系统配置";
      terminal.style.marginRight = "20px";
    } else if (this.diySettingMapper.appLogSys == 1) {
      terminal.innerHTML = "关";
    } else if (this.diySettingMapper.appLogSys == 2) {
      terminal.innerHTML = "开";
    }
    //调度员app日志记录开关
    let applog = document.getElementById("applog");
    if (this.diySettingMapper.dispatcherLogSys == 0) {
      applog.innerHTML = "跟随系统配置";
      applog.style.marginRight = "20px";
    } else if (this.diySettingMapper.dispatcherLogSys == 1) {
      applog.innerHTML = "关";
    } else if (this.diySettingMapper.dispatcherLogSys == 2) {
      applog.innerHTML = "开";
    }
    //企业存储空间报警配置
    let store = document.getElementById("store");
    if (this.diySettingMapper.storeTipSys == 0) {
      store.innerHTML = "跟随系统配置";
      store.style.marginRight = "20px";
    } else if (this.diySettingMapper.storeTipSys == 1) {
      store.innerHTML = this.diySettingMapper.storeTip + "GB";
    }

    //查询企业存储空间
    const storageData = await this.companyStorageAsync({
      bid: data.businessManager.bid
    });
    this.storageTalkBack = storageData.talkBack;
    this.storageBroadcast = storageData.broadcast;
    this.storageImages = storageData.images;
    this.storageLog = storageData.log;
    this.storageVideo = storageData.video;
    this.storagePhoneRecord = storageData.phoneRecord;
    this.storageUsed = storageData.used;
  },
  computed: {
    ...mapState(["businessManager", "diySettingMapper", "queryData"])
  },
  methods: {
    ...mapActions([
      "companyDetailAsync",
      "statisticAsync",
      "companyStorageAsync",
      "clearStorageAsync"
    ]),
    jump() {
      this.companyid = this.clearAccount;
      this.companyUrl =
        "https://qiye.ytha-hd.com" +
        "?" +
        "action=" +
        this.companyid +
        "&" +
        "id=" +
        this.companydata;
    },
    handleClick(tab, event) {
      console.log(tab.label);
      if (tab.label=='企业统计') {   
        let location = '';
        let id = JSON.parse(sessionStorage.getItem("company")).bid;
        this.statisticAsync({ address: location, bid: id });
      }
    },
    location(value) {
      let province;
      let city;
      let area;
      // console.log(value[0]);
      if (CodeToText[value[0]] == "全部") {
        province = "";
        city = "";
        area = "";
        this.locations = province + city + area;
      } else {
        province = CodeToText[value[0]];

        if (CodeToText[value[1]] == "全部") {
          city = "";
          area = "";
          this.locations = province + city + area;
        } else {
          city = CodeToText[value[1]];
          if (CodeToText[value[2]] == "全部") {
            area = "";
            this.locations = province + city + area;
          } else {
            area = CodeToText[value[2]];
            this.locations = province + city + area;
          }
        }
      }
    },
   async query() {
      let locations = this.locations;
      let id = JSON.parse(sessionStorage.getItem("company")).bid;
     const data=await this.statisticAsync({ address: locations, bid: id });     
     if (data.code==403) {
       this.$message("该地址无数据")
     }
    },
    //清空储存空间
    async sureClear() {
      let clearValue = this.clearValue;
      let storageBid = this.storageBid;
      let checkType = [];
      for (let i in this.checkType) {
        checkType.push(this.checkType[i]);
      }
      let typevalue = checkType.map(Number);
      let typeOps = JSON.stringify(typevalue);
      const data = await this.clearStorageAsync({
        bid: storageBid,
        option: typeOps,
        days: clearValue
      });
      if (data.code == 200) {
        this.$message("清除成功");
      } else {
        this.$message("清除失败，请联系管理员");
      }
    }
  }
};
</script>

<style scoped>
.detailContainer {
  width: 96%;
  height: 900px;
  margin: 0 auto;
}
.first {
  width: 100%;
  height: 760px;
  overflow-y: auto;
}
.first div {
  margin-bottom: 20px;
  font-size: 14px;
  display: flex;
}
.first i {
  font-style: normal;
  display: inline-block;
  width: 200px;
  margin-right: 40px;
}
/* .first div span{
margin-right: 20px;
} */
.btn {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 50px;
  height: 26px;
  text-align: center;
  margin-left: 20px;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
  text-decoration: none;
  line-height: 26px;
}
.btn:hover {
  cursor: pointer;
}
.second span {
  font-size: 14px;
  margin-right: 30px;
}
.second button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 80px;
  height: 38px;
  text-align: center;
  margin-left: 30px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.second button:hover {
  cursor: pointer;
}
.box0 {
  width: 99%;
  height: 200px;
  border: 1px solid rgb(192, 188, 188);
  margin-top: 20px;
}
.box1 {
  width: 100%;
  height: 170px;
  display: flex;
}
.box1 > div {
  width: 33.3%;
  height: 100%;
  text-align: center;
}
.title {
  font-size: 14px;
  font-weight: 600;
  padding-left: 20px;
}
.count {
  font-size: 28px;
  font-weight: 600;
}
.num {
  font-size: 16px;
  font-weight: 500;
}
.thirdBox {
  width: 99%;
  margin: 20px auto;
  display: flex;
  height: 300px;
  border: 1px solid rgb(192, 188, 188);
}
.thirdBox > div {
  width: 14%;
  height: 200px;
  margin-top: 75px;
  text-align: center;
}
.thirdBox > div:nth-of-type(1) {
  width: 16%;
  margin-top: 50px;
  border-right: 1px solid rgb(192, 188, 188);
}
.p1 {
  font-size: 14px;
}
.use {
  font-size: 24px;
  font-weight: 600;
}
.type {
  font-size: 16px;
  font-weight: 500;
}
.clear {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 80px;
  height: 38px;
  text-align: center;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.clear:hover {
  cursor: pointer;
}
.tips {
  padding-left: 76px;
  color: gray;
  font-size: 12px;
}
.otherDiv {
  margin-bottom: 0 !important;
}
.description {
  width: 1200px;
  height: 80px;
  margin: 0;
  padding: 0;
  margin-bottom: 0;
  overflow-y: auto;
}

.otherD {
  display: inline-block;
  width: 200px;
  height: 80px;
}
</style>

<style>
.detailContainer .el-breadcrumb {
  text-align: center;
  line-height: 30px;
  padding-top: 10px;
}
.detailContainer .el-breadcrumb__inner a,
.el-breadcrumb__inner.is-link {
  font-size: 20px;
}
.detailContainer .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  font-size: 16px;
}
.detailContainer .el-dialog__header {
  text-align: left;
}
.detailContainer .el-dialog__body {
  text-align: left;
}
</style>