<template>
  <div class="otherSet">
    <p>其他设置</p>
    <div class="otherform">
      <el-form
        :model="ruleForm"
        ref="ruleForm"
        label-width="270px"
        class="demo-ruleForm"
        label-position="left"
      >
        <el-form-item label="终端视频录制时长限制" prop="longtime">
          <el-radio-group v-model="ruleForm.longtime" @change="finalTime">
            <el-radio label="无限制"></el-radio>
            <el-radio label="限制" ></el-radio>
          </el-radio-group>
          <el-input placeholder="500" :disabled="one" v-model="timeLimit"></el-input>
          <span>分钟</span>
          <i>（到达该时长自动结束录制，最大不可超过1440分钟）</i>
        </el-form-item>
        <el-form-item label="终端视频自动上传限制" prop="video">
          <el-radio-group v-model="ruleForm.video" @change="autoUpload">
            <el-radio label="无限制"></el-radio>
            <el-radio label="限制"></el-radio>
          </el-radio-group>
          <el-input placeholder="1000" :disabled="two" v-model="videoLimit"></el-input>
          <span>MB</span>
          <i>（大于该值的视频不自动上传）</i>
        </el-form-item>
        <el-form-item label="调度员app视频自动上传限制" prop="appvideo">
          <el-radio-group v-model="ruleForm.appvideo" @change="appUpload">
            <el-radio label="无限制"></el-radio>
            <el-radio label="限制"></el-radio>
          </el-radio-group>
          <el-input placeholder="1000" :disabled="three" v-model="appLimit"></el-input>
          <span>MB</span>
          <i>（在非wifi网络下大于该值的视频不自动上传，wifi网络下不受限制）</i>
        </el-form-item>
        <el-form-item label="终端日志记录开关" prop="log">
          <el-radio-group v-model="ruleForm.log" @change="logSwitch">
            <el-radio label="关"></el-radio>
            <el-radio label="开"></el-radio>
          </el-radio-group>
          <el-input placeholder="5" :disabled="four" v-model="switchLimit"></el-input>
          <span>天自动清理</span>
          <i>（如设置5天，则每5天清空日志；设置为0时不自动清理）</i>
        </el-form-item>
        <el-form-item label="调度员app日志记录开关" prop="applog">
          <el-radio-group v-model="ruleForm.applog" @change="logApp">
            <el-radio label="关"></el-radio>
            <el-radio label="开"></el-radio>
          </el-radio-group>
          <el-input placeholder="5" :disabled="five" v-model="appLogLimit"></el-input>
          <span>天自动清理</span>
          <i>（如设置5天，则每5天清空日志；设置为0时不自动清理）</i>
        </el-form-item>
        <el-form-item label="企业服务时间到期提示设置" prop="remind">
          <el-radio-group v-model="ruleForm.remind" @change="companyRe">
            <el-radio label="不提示"></el-radio>
            <el-radio label="提示"></el-radio>
          </el-radio-group>
          <el-input placeholder="5" :disabled="six" v-model="companyDate"></el-input>
          <span>天</span>
          <i>（距离企业服务时间到期提示的天数）</i>
        </el-form-item>
        <el-form-item label="第三方企业服务时间到期提示设置" prop="remindOther">
          <el-radio-group v-model="ruleForm.remindOther" @change="otherRe">
            <el-radio label="不提示"></el-radio>
            <el-radio label="提示"></el-radio>
          </el-radio-group>
          <el-input placeholder="5" :disabled="seven" v-model="thirdCompany"></el-input>
          <span>天</span>
          <i>（距离第三方企业服务时间到期提示的天数）</i>
        </el-form-item>
        <el-form-item label="企业存储空间报警设置" prop="warn">
          <input type="text" placeholder="400" class="warn" v-model="ruleForm.warn" />
          <span>GB</span>
          <i>（企业存储空间大于该值则会触发一次报警通知）</i>
        </el-form-item>
          <p>*若选择提示或开按钮，输入框中未输入值时，按默认值提交。</p>
        <el-form-item>
          <el-button type="primary" @click="update">保存修改</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("system");
export default {
  data() {
    return {
      ruleForm: {
        longtime: "",
        video: "",
        appvideo: "",
        log: "",
        applog: "",
        remind: "",
        remindOther: "",
        warn: 500
      },
      timeLimit: 60,
      one: true,
      videoLimit: 500,
      two: true,
      appLimit: 500,
      three: true,
      switchLimit: 5,
      four: true,
      appLogLimit: 5,
      five: true,
      companyDate: 30,
      six: true,
      thirdCompany: 30,
      seven: true
    };
  },
  async mounted() {
    const data = await this.otherSetAsync();    
    //终端视频录制时长限制
    if (data.vedioTimeLimit == 0) {
      this.ruleForm.longtime = "无限制";
      this.timeLimit = 0;
    } else {
      this.ruleForm.longtime = "限制";
      this.one = false;
      this.timeLimit = data.vedioTimeLimit;
    }
    //终端视频自动上传限制
    if (data.isAutoUpload == 0) {
      this.ruleForm.video = "无限制";
      this.videoLimit = 0;
    } else {
      this.ruleForm.video = "限制";
      this.two = false;
      this.videoLimit = data.isAutoUpload;
    }
    //调度员app视频自动上传限制
    if (data.dispatcherUploadLimit == 0) {
      this.ruleForm.appvideo = "无限制";
      this.appLimit = 0;
    } else {
      this.ruleForm.appvideo = "限制";
      this.three = false;
      this.appLimit = data.dispatcherUploadLimit;
    }
    //终端日志记录开关
    if (data.appLog == 0) {
      this.ruleForm.log = "关";
      this.switchLimit = 0;
    } else {
      this.ruleForm.log = "开";
      this.four = false;
      this.switchLimit = data.appLog;
    }
    //调度员app日志记录开关
    if (data.dispatcherLog == 0) {
      this.ruleForm.applog = "关";
      this.appLogLimit = 0;
    } else {
      this.ruleForm.applog = "开";
      this.five = false;
      this.appLogLimit = data.dispatcherLog;
    }
    //企业服务时间到期提示设置
    if (data.tipTime == 0) {
      this.ruleForm.remind = "不提示";
      this.companyDate = 0;
    } else {
      this.ruleForm.remind = "提示";
      this.six = false;
      this.companyDate = data.tipTime;
    }
    //第三方企业服务时间到期提示设置
    if (data.thirdTipTime == 0) {
      this.ruleForm.remindOther = "不提示";
      this.thirdCompany = 0;
    } else {
      this.ruleForm.remindOther = "提示";
      this.seven = false;
      this.thirdCompany = data.thirdTipTime;
    }
    //终端视频录制时长限制
    this.ruleForm.warn = data.storeTip;
  },
  methods: {
    ...mapActions(["otherSetAsync", "otherUpdateAsync"]),
    //终端视频录制时长限制
    finalTime() {
      if (this.ruleForm.longtime == "无限制") {
        this.timeLimit = 0;
        this.one = true;
      } else if (this.ruleForm.longtime == "限制") {
        this.one = false;
      }
    },
    //终端视频自动上传限制
    autoUpload() {
      if (this.ruleForm.video == "无限制") {
        this.videoLimit = 0;
        this.two = true;
      } else if (this.ruleForm.video == "限制") {
        this.two = false;
      }
    },
    //调度员app视频自动上传限制
    appUpload() {
      if (this.ruleForm.appvideo == "无限制") {
        this.appLimit = 0;
        this.three = true;
      } else if (this.ruleForm.appvideo == "限制") {
        this.three = false;
      }
    },
    //终端日志记录开关
    logSwitch() {
      if (this.ruleForm.log == "关") {
        this.switchLimit = 0;
        this.four = true;
      } else if (this.ruleForm.log == "开") {
        this.four = false;
      }
    },
    //调度员app日志记录开关
    logApp() {
      if (this.ruleForm.applog == "关") {
        this.appLogLimit = 0;
        this.five = true;
      } else if (this.ruleForm.applog == "开") {
        this.five = false;
      }
    },
    //企业服务时间到期提示设置
    companyRe() {
      if (this.ruleForm.remind == "不提示") {
        this.companyDate = 0;
        this.six = true;
      } else if (this.ruleForm.remind == "提示") {
        this.six = false;
      }
    },
    //第三方企业服务时间到期提示设置
    otherRe() {
      if (this.ruleForm.remindOther == "不提示") {
        this.thirdCompany = 0;
        this.seven = true;
      } else if (this.ruleForm.remindOther == "提示") {
        this.seven = false;
      }
    },
    async update() {
      let time=this.timeLimit;
      let upload=this.videoLimit;
      let uploadApp=this.appLimit;
      let log=this.switchLimit;      
      let logApp=this.appLogLimit;
      let companyT=this.companyDate;
      let thirdT=this.thirdCompany;
      let store=this.ruleForm.warn;
      const data=await this.otherUpdateAsync({
        vedioTimeLimit:time,
        isAutoUpload:upload,
        dispatcherUploadLimit:uploadApp,
        appLog:log,
        dispatcherLog:logApp,
        tipTime:companyT,
        thirdTipTime:thirdT,
        storeTip:store
      });
      if (data.code==200) {
        this.$message("修改成功");
      }
    }
  }
};
</script>

<style scoped>
.otherSet {
  width: 96%;
  margin: 0 auto;
}
.otherSet > p {
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(192, 188, 188);
}
.otherform {
  width: 100%;
  height: 770px;
  overflow-y: auto;
  padding-top: 15px;
  background-color: #ffffff;
}
.button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 140px;
  height: 40px;
  text-align: center;
  padding: 10px 15px;
  margin-left: 123px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.button:hover {
  cursor: pointer;
}
.otherform input {
  margin-left: 30px;
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 85px;
  height: 32px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 10px;
}
.otherform .warn {
  margin-left: 0;
}
.otherform span {
  display: inline-block;
  margin-left: 10px;
}
.otherform i {
  display: inline-block;
  font-style: normal;
  margin-left: 30px;
  color: rgba(153, 153, 153, 1);
}
.otherform p {
  color: rgba(153, 153, 153, 1);
}
</style>
<style>
.otherSet .el-breadcrumb {
  text-align: center;
  line-height: 30px;
  padding-top: 10px;
}
.otherSet .el-breadcrumb__inner a,
.el-breadcrumb__inner.is-link {
  font-size: 20px;
}
.otherSet .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  font-size: 16px;
}
.otherform .el-form .el-input {
  width: 100px;
  margin-left: 15px;
}
.otherform .el-form .el-input__inner {
  width: 100px;
}
.otherform .el-form .el-form-item__content {
  width: 800px;
}
