<template>
  <div class="othersUpdate">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Index/deploy' }">独立部署授权管理</el-breadcrumb-item>
      <el-breadcrumb-item>修改第三方企业</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="form">
      <el-form :model="ruleForm" ref="ruleForm" label-width="200px" class="demo-ruleForm">
        <el-form-item label="企业名称" prop="bname">
          <el-input v-model="ruleForm.bname" placeholder="限20字"></el-input>
        </el-form-item>
        <el-form-item label="企业地址" prop="area" required>
          <!-- <el-cascader v-model="area" :options="areaOption" @change="handleChange"></el-cascader> -->
          
          <el-input
            v-model="ruleForm.dAdress"
            placeholder="请输入详细地址地图定位"
            
            id="suggestId"
          ></el-input>
          <!-- 百度地图 -->
          <div
            id="searchResultPanel"
            style="border:1px solid #C0C0C0;width:150px;height:auto; display:none;"
          ></div>
          <baidu-map
            :center="center"
            :zoom="zoom"
            @ready="handler"
            class="baiduMap"
            :scroll-wheel-zoom="true"
            id="l-map"
          ></baidu-map>
          <el-input v-model="ruleForm.area" placeholder="请输入详细地址(必须输入省)，限40字,此处必填项" class="address"></el-input>
        </el-form-item>
        <el-form-item label="负责人" prop="personInCharge">
          <el-input v-model="ruleForm.personInCharge" placeholder="限20字"></el-input>
        </el-form-item>
        <el-form-item label="负责人电话" prop="picPhone">
          <el-input v-model="ruleForm.picPhone"></el-input>
        </el-form-item>
        <el-form-item label="服务到期时间" required>
          <el-date-picker
            v-model="ruleForm.validityTime"
            type="datetime"
            placeholder="选择日期时间"
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="合作状态" prop="state" required>
          <el-select v-model="ruleForm.state" placeholder="请选择合作状态" @change="statusChange">
            <el-option label="合作中" value="0"></el-option>
            <el-option label="暂停合作" value="1"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="其他说明" prop="description">
          <el-input type="textarea" v-model="ruleForm.description" placeholder="限500字" maxlength="500"></el-input>
        </el-form-item>
        <el-form-item label="终端账号数限制" prop="deviceNumLimit" required>
          <el-input v-model="ruleForm.deviceNumLimit"></el-input>
        </el-form-item>
        <el-form-item label="绑定服务器IP" prop="serverHost" required>
          <el-input v-model="ruleForm.serverHost"></el-input>
        </el-form-item>
        <el-form-item label="绑定服务器MAC地址" prop="serverMac" required>
          <el-input v-model="ruleForm.serverMac"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="update">确认修改</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { regionData, CodeToText, TextToCode } from "element-china-area-data";
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("deploy");
export default {
  data() {
    return {
      ruleForm: {
        bname: "",
        dAdress: "",
        personInCharge: "",
        picPhone: "",
        state: "",
        description: "",
        deviceNumLimit: "",
        serverHost: "",
        serverMac: "",
        validityTime: "",
        bid: 0,
        area:""
      },
      input: "",
      areaOption: regionData,
      center: { lng: 104, lat: 30 },
      zoom: 13,
      lons:'',
      lats:''
    };
  },
  async mounted() {
    let id = JSON.parse(sessionStorage.getItem("otherUpdateID"));
    const data = await this.thirdDetailAsync({ bid: id });
    Object.assign(this.ruleForm, data);
    this.ruleForm.area = data.address;
    if (data.state == 0) {
      this.ruleForm.state = "合作中";
    } else if (data.state == 1) {
      this.ruleForm.state = "暂停合作";
    }
  },
  methods: {
    ...mapActions(["updateThirdAsync", "deployFormAsync", "thirdDetailAsync"]),
    // handleChange(value) {
    //   let province = CodeToText[value[0]];
    //   let city = CodeToText[value[1]];
    //   let area = CodeToText[value[2]];
    //   this.location = province + city + area;
    // },
    statusChange(){
        if (this.ruleForm.state = "暂停合作") {
          this.status=1
        }else if (this.ruleForm.state = "合作中") {
          this.status=0
        }
    },
    async update() {
      let locations = this.ruleForm.area;
      let id = JSON.parse(sessionStorage.getItem("otherUpdateID"));
      console.log(id);
      
      let lon;
      let lat;
      if (this.ruleForm.dAdress=='') {
         lon = this.lons;
         lat = this.lats;
      }else{
        lon=window.ppp.lng;
        lat=window.ppp.lat;
      }
      let status=this.status;

      //合并地址
      const data = await this.updateThirdAsync({
        ...this.ruleForm,
        address: locations,
        bid: id,
        longitude:lon,
        latitude:lat,
        state:status
      });
      if (data.code == 200) {
        this.$message("修改成功");
        this.deployFormAsync();
      } else if (data.code == 202) {
        this.$message("用户名已经存在");
      } else if (data.code == 207) {
        this.$message("主账号格式错误");
      }
    },
     //百度地图
    handler({ BMap, map }) {
      // 百度地图API功能
      function G(id) {
        return document.getElementById(id);
      }

      var mapS = new BMap.Map("l-map");
      var point = new BMap.Point(104.081548, 30.652613);
      mapS.centerAndZoom(point, 5); // 初始化地图,设置城市和地图级别。

      var ac = new BMap.Autocomplete({ input: "suggestId", location: mapS }); //建立一个自动完成的对象

      ac.addEventListener("onhighlight", function(e) {
        //鼠标放在下拉列表上的事件
        var str = "";
        var _value = e.fromitem.value;
        var value = "";
        if (e.fromitem.index > -1) {
          value =
            _value.province +
            _value.city +
            _value.district +
            _value.street +
            _value.business;
        }
        str =
          "FromItem<br />index = " +
          e.fromitem.index +
          "<br />value = " +
          value;

        value = "";
        if (e.toitem.index > -1) {
          _value = e.toitem.value;
          value =
            _value.province +
            _value.city +
            _value.district +
            _value.street +
            _value.business;
        }
        str +=
          "<br />ToItem<br />index = " +
          e.toitem.index +
          "<br />value = " +
          value;
        G("searchResultPanel").innerHTML = str;
      });

      var myValue;
      ac.addEventListener("onconfirm", function(e) {
        //鼠标点击下拉列表后的事件
        var _value = e.item.value;
        myValue =
          _value.province +
          _value.city +
          _value.district +
          _value.street +
          _value.business;
        G("searchResultPanel").innerHTML =
          "onconfirm<br />index = " +
          e.item.index +
          "<br />myValue = " +
          myValue;

        setPlace();
      });
      function setPlace() {
        mapS.clearOverlays(); //清除地图上所有覆盖物
        function myFun() {
          var pp = local.getResults().getPoi(0).point; //获取第一个智能搜索的结果
          mapS.centerAndZoom(pp, 18);
          mapS.addOverlay(new BMap.Marker(pp)); //添加标注
          window.ppp=pp;
        }
        var local = new BMap.LocalSearch(mapS, {
          //智能搜索
          onSearchComplete: myFun
        });
        local.search(myValue);
      }
    }
  }
};
</script>

<style scoped>
.othersUpdate {
  width: 96%;
  margin: 0 auto;
}
.form {
  width: 100%;
  height: 770px;
  overflow-y: auto;
  padding-top: 15px;
  background-color: #ffffff;
}
.button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 140px;
  height: 40px;
  text-align: center;
  padding: 10px 15px;
  margin-left: 123px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.button:hover {
  cursor: pointer;
}
.form input {
  margin-left: 30px;
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 85px;
  height: 32px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 10px;
}
.form span {
  display: inline-block;
  margin-left: 10px;
}
.form i {
  display: inline-block;
  font-style: normal;
  margin-left: 30px;
  color: rgba(153, 153, 153, 1);
}
.form p {
  color: rgba(153, 153, 153, 1);
}
/* 地图容器 */
#l-map {
  width: 400px;
  height: 300px;
  margin-top: 20px;
}
</style>

<style>
.othersUpdate .el-breadcrumb {
  text-align: center;
  line-height: 30px;
  padding-top: 10px;
}
.othersUpdate .el-breadcrumb__inner a,
.el-breadcrumb__inner.is-link {
  font-size: 20px;
}
.othersUpdate .el-breadcrumb__item:last-child .el-breadcrumb__inner {
  font-size: 16px;
}

.form .el-form .el-input__inner {
  width: 400px;
}
.form .el-form .el-form-item__content {
  width: 800px;
}
.form .el-form .address {
  margin-top: 15px;
}
.othersUpdate .el-textarea {
    width: 400px;
}
</style>