<template>
  <div class="updateContainer">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/Index/company' }">企业管理</el-breadcrumb-item>
      <el-breadcrumb-item>修改企业</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="form">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="220px"
        class="demo-ruleForm"
      >
        <div class="first">
          <el-form-item label="企业名称" prop="bname">
            <el-input v-model="ruleForm.bname" placeholder="限20字"></el-input>
          </el-form-item>
          <el-form-item label="主账号" prop="mainAccount">
            <el-input v-model="ruleForm.mainAccount" placeholder="3-10位数字、字母、字符" :disabled="true"></el-input>
          </el-form-item>
          <p class="ptips">主账号添加后不可修改，且不可重复，初始密码与账号相同</p>
          <el-form-item label="企业地址" prop="address">
            <!-- 百度地图 -->
            <label>
              <el-cascader
                v-model="ruleForm.area"
                :options="areaOption"
                @change="handleChange"
                clearable
              ></el-cascader>
            </label>
            <el-input
              v-model="ruleForm.address"
              placeholder="请输入详细地址"
              @input="inputOnInput($event)"
              id="suggestId"
            ></el-input>
          </el-form-item>
          <el-form-item style="marginTop:-15px;">
            <baidu-map
              :center="center"
              :zoom="zoom"
              @ready="handler"
              class="baiduMap"
              :scroll-wheel-zoom="true"
              id="l-map"
            >
              <bm-local-search :keyword="arealocation" :auto-viewport="true" :panel="false"></bm-local-search>
              <bm-local-search
                :keyword="ruleForm.address"
                :auto-viewport="true"
                :panel="false"
                @searchcomplete="getGps"
              ></bm-local-search>
            </baidu-map>
          </el-form-item>
          <el-form-item label="负责人" prop="personInCharge">
            <el-input v-model="ruleForm.personInCharge" placeholder="限20字"></el-input>
          </el-form-item>
          <el-form-item label="负责人电话" prop="picPhone">
            <el-input v-model="ruleForm.picPhone"></el-input>
          </el-form-item>
          <el-form-item label="服务到期时间" prop="validityTime">
            <el-date-picker
              v-model="ruleForm.validityTime"
              type="date"
              placeholder="选择日期时间"
              value-format="yyyy-MM-dd"
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="合作状态" prop="status">
            <el-select v-model="ruleForm.status" placeholder="请选择合作状态" @change="sta">
              <el-option label="合作中" value="0"></el-option>
              <el-option label="暂停合作" value="1"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="其他说明" prop="description">
            <el-input
              type="textarea"
              v-model="ruleForm.description"
              placeholder="限500字"
              maxlength="500"
            ></el-input>
          </el-form-item>
          <el-form-item label="终端账号数限制" prop="deviceNumLimit">
            <el-input v-model="ruleForm.deviceNumLimit"></el-input>
          </el-form-item>
        </div>
        <div class="second">
          <el-form-item label="功能配置" required>
            <div class="checkGroup">
              <el-checkbox label="语音通话功能" v-model="ruleForm.voice" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="视频通话功能" v-model="ruleForm.see" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="对讲功能" v-model="ruleForm.talk" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="电子围栏功能" v-model="ruleForm.rail" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="轨迹功能" v-model="ruleForm.track" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="广播功能" v-model="ruleForm.broad" true-label="0" false-label="1"></el-checkbox>
              <el-checkbox label="无声侦护功能" v-model="ruleForm.detect" true-label="0" false-label="1"></el-checkbox>
            </div>
          </el-form-item>
          <el-form-item label="终端视频录制时长限制" prop="time" required>
            <el-radio-group v-model="ruleForm.time" @change="finalTime">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="无限制"></el-radio>
              <el-radio label="限制"></el-radio>
            </el-radio-group>
            <el-input placeholder="200" :disabled="one" v-model="ruleForm.timeLimit"></el-input>
            <span>分钟</span>
            <i>（到达该时长自动结束录制，最大不可超过1440分钟）</i>
          </el-form-item>
          <el-form-item label="终端视频自动上传限制" prop="upload" required>
            <el-radio-group v-model="ruleForm.upload" @change="videoUpload">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="无限制"></el-radio>
              <el-radio label="限制"></el-radio>
            </el-radio-group>
            <el-input placeholder="1000" :disabled="two" v-model="ruleForm.videoLimit"></el-input>
            <span>MB</span>
            <i>（大于该值的视频不自动上传）</i>
          </el-form-item>
          <el-form-item label="调度员app视频自动上传限制" prop="app" required>
            <el-radio-group v-model="ruleForm.app" @change="appUpload">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="无限制"></el-radio>
              <el-radio label="限制"></el-radio>
            </el-radio-group>
            <el-input placeholder="1000" :disabled="three" v-model="ruleForm.appLimit"></el-input>
            <span>MB</span>
            <i>（在非wifi网络下大于该值的视频不自动上传，wifi网络下不受限制）</i>
          </el-form-item>
          <el-form-item label="终端日志记录开关" prop="switch" required>
            <el-radio-group v-model="ruleForm.switch" @change="logSwitch">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="关"></el-radio>
              <el-radio label="开"></el-radio>
            </el-radio-group>
            <el-input placeholder="5" :disabled="four" v-model="ruleForm.switchLimit"></el-input>
            <span>天自动清理</span>
            <i>（如设置5天，则每5天清空日志；设置为0时不自动清理）</i>
          </el-form-item>
          <el-form-item label="调度员app日志记录开关" prop="log" required>
            <el-radio-group v-model="ruleForm.log" @change="appLog">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="关"></el-radio>
              <el-radio label="开"></el-radio>
            </el-radio-group>
            <el-input placeholder="5" :disabled="five" v-model="ruleForm.appLogLimit"></el-input>
            <span>天自动清理</span>
            <i>（如设置5天，则每5天清空日志；设置为0时不自动清理）</i>
          </el-form-item>
          <el-form-item label="企业存储空间报警配置" prop="save" required>
            <el-radio-group v-model="ruleForm.save" @change="savation">
              <el-radio label="跟随系统设置"></el-radio>
              <el-radio label="自定义"></el-radio>
            </el-radio-group>
            <el-input placeholder="500" :disabled="six" v-model="ruleForm.saveLimit"></el-input>
            <span>GB</span>
            <i>（企业存储空间大于该值则会触发一次报警通知）</i>
          </el-form-item>
        </div>
        <el-form-item>
          <el-button type="primary" @click="add">确认修改</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { regionData, CodeToText, TextToCode } from "element-china-area-data";
import { createNamespacedHelpers } from "vuex";
import { BmlMarkerClusterer } from "vue-baidu-map";
import { log } from "util";
const { mapState, mapActions } = createNamespacedHelpers("company");
export default {
  data() {
    return {
      ruleForm: {
        bname: "",
        mainAccount: "",
        area: "",
        address: "",
        personInCharge: "",
        picPhone: "",
        status: "",
        description: "",
        deviceNumLimit: 100,
        validityTime: "",
        voice: 1,
        see: 1,
        talk: 1,
        rail: 1,
        track: 1,
        broad: 1,
        detect: 1,
        time: "",
        timeLimit: 60,
        upload: "",
        videoLimit: 1000,
        app: "",
        appLimit: 500,
        switch: "",
        switchLimit: 20,
        log: "",
        appLogLimit: 20,
        save: "",
        saveLimit: 100
      },
      lons: "",
      lats: "",
      areaOption: regionData,
      one: true,
      two: true,
      three: true,
      four: true,
      five: true,
      six: true,
      center: { lng: 104, lat: 30 },
      zoom: 13,
      location: "北京",
      keyword: "百度",
      arealocation: "",
      rules: {
        bname: [
          { required: true, message: "请输企业名称", trigger: "blur" },
          { min: 1, max: 20, message: "长度不能超过20个字符", trigger: "blur" }
        ],
        mainAccount: [
          { required: true, message: "请输入主账号", trigger: "blur" },
          {
            min: 1,
            max: 10,
            message: "3-10位数字、字母、字符",
            trigger: "blur"
          }
        ],
        address: [
          { required: true, message: "请输入详细地址", trigger: "blur" }
        ],
        personInCharge: [
          { required: true, message: "请输入负责人", trigger: "blur" }
        ],
        picPhone: [
          { required: true, message: "请输入负责人电话", trigger: "blur" }
        ],
        validityTime: [
          { required: true, message: "请选择服务到期时间", trigger: "blur" }
        ],
        status: [
          { required: true, message: "请选择合作状态", trigger: "blur" }
        ],
        description: [{ max: 500, message: "限500字", trigger: "blur" }],
        deviceNumLimit: [
          {
            required: true,
            message: "请输入终端账号数限制",
            trigger: "blur"
          }
        ]
      }
    };
  },
  async mounted() {
    let id = JSON.parse(sessionStorage.getItem("updateID"));
    const data = await this.companyDetailAsync(id);
    this.did = data.diySettingMapper.did;
    let company = Object.assign(data.businessManager, data.diySettingMapper);
    let newfrom = Object.assign(this.ruleForm, company);
    console.log(company);
    this.setlongitude = company.longitude;
    this.setlatitude = company.latitude;
    this.ruleForm.address = company.address;
    //合作状态
    if (company.status == 0) {
      this.ruleForm.status = "合作中";
    } else if (company.status == 1) {
      this.ruleForm.status = "暂停合作";
    }
    //加载时功能设置
    if (company.isCall == "0") {
      this.ruleForm.voice = "0";
    } else {
      this.ruleForm.voice = "";
    }
    if (company.vedio == "0") {
      this.ruleForm.see = "0";
    } else {
      this.ruleForm.see = "";
    }
    if (company.talkback == "0") {
      this.ruleForm.talk = "0";
    } else {
      this.ruleForm.talk = "";
    }
    if (company.mapRange == "0") {
      this.ruleForm.rail = "0";
    } else {
      this.ruleForm.rail = "";
    }
    if (company.trail == "0") {
      this.ruleForm.track = "0";
    } else {
      this.ruleForm.track = "";
    }
    if (company.broadcast == "0") {
      this.ruleForm.broad = "0";
    } else {
      this.ruleForm.broad = "";
    }
    if (company.listener == "0") {
      this.ruleForm.detect = "0";
    } else {
      this.ruleForm.detect = "";
    }
    //加载时终端视频录制时长限制
    if (company.vedioTimeLimitSys == 0) {
      this.ruleForm.time = "跟随系统设置";
    } else if (company.vedioTimeLimitSys == 1) {
      this.ruleForm.time = "无限制";
    } else if (company.vedioTimeLimitSys == 2) {
      this.ruleForm.time = "限制";
      this.ruleForm.timeLimit = company.vedioTimeLimit;
      this.one = false;
    }
    //终端视频自动上传限制
    if (company.isAutoUploadSys == 0) {
      this.ruleForm.upload = "跟随系统设置";
    } else if (company.isAutoUploadSys == 1) {
      this.ruleForm.upload = "无限制";
    } else if (company.isAutoUploadSys == 2) {
      this.ruleForm.upload = "限制";
      this.ruleForm.videoLimit = company.isAutoUpload;
      this.two = false;
    }
    //调度员app视频自动上传限制
    if (company.dispatcherUploadLimitSys == 0) {
      this.ruleForm.app = "跟随系统设置";
    } else if (company.dispatcherUploadLimitSys == 1) {
      this.ruleForm.app = "无限制";
    } else if (company.dispatcherUploadLimitSys == 2) {
      this.ruleForm.app = "限制";
      this.ruleForm.appLimit = company.dispatcherUploadLimit;
      this.three = false;
    }
    //终端日志记录开关
    if (company.appLogSys == 0) {
      this.ruleForm.switch = "跟随系统设置";
    } else if (company.appLogSys == 1) {
      this.ruleForm.switch = "关";
    } else if (company.appLogSys == 2) {
      this.ruleForm.switch = "开";
      this.ruleForm.switchLimit = company.appLog;
      this.four = false;
    }
    //调度员app日志记录开关
    if (company.dispatcherLogSys == 0) {
      this.ruleForm.log = "跟随系统设置";
    } else if (company.dispatcherLogSys == 1) {
      this.ruleForm.log = "关";
    } else if (company.dispatcherLogSys == 2) {
      this.ruleForm.log = "开";
      this.ruleForm.appLogLimit = company.dispatcherLog;
      this.five = false;
    }
    //企业存储空间报警配置
    if (company.storeTipSys == 0) {
      this.ruleForm.save = "跟随系统设置";
    } else if (company.storeTipSys == 1) {
      this.ruleForm.save = "自定义";
      this.ruleForm.saveLimit = company.storeTip;
      this.six = false;
    }
  },
  methods: {
    ...mapActions(["updateCompanyAsync", "formAsync", "companyDetailAsync"]),
    handleChange(value) {
      let province = CodeToText[value[0]];
      let city = CodeToText[value[1]];
      let area = CodeToText[value[2]];
      this.location = province + city + area;
      this.ruleForm.address = this.location;
      this.changelocationflag = true;
    },
    //合作状态
    sta() {
      this.staus = this.ruleForm.status;
    },
    //终端视频录制时长限制
    finalTime() {
      if (this.ruleForm.time == "跟随系统设置") {
        this.videoTime = 0;
        this.ruleForm.timeLimit = "";
        this.one = true;
      } else if (this.ruleForm.time == "无限制") {
        this.videoTime = 1;
        this.ruleForm.timeLimit = "";
        this.one = true;
      } else if (this.ruleForm.time == "限制") {
        this.videoTime = 2;
        this.one = false;
      }
    },
    //终端视频自动上传限制
    videoUpload() {
      console.log(this.ruleForm.upload);
      if (this.ruleForm.upload == "跟随系统设置") {
        this.uploadVideo = 0;
        this.ruleForm.videoLimit = "";
        this.two = true;
      } else if (this.ruleForm.upload == "无限制") {
        this.uploadVideo = 1;
        this.ruleForm.videoLimit = "";
        this.two = true;
      } else if (this.ruleForm.upload == "限制") {
        this.uploadVideo = 2;
        this.two = false;
      }
    },
    //调度员app视频自动上传限制
    appUpload() {
      if (this.ruleForm.app == "跟随系统设置") {
        this.appVideo = 0;
        this.ruleForm.appLimit = "";
        this.three = true;
      } else if (this.ruleForm.app == "无限制") {
        this.appVideo = 1;
        this.ruleForm.appLimit = "";
        this.three = true;
      } else if (this.ruleForm.app == "限制") {
        this.appVideo = 2;
        this.three = false;
      }
    },
    //终端日志记录开关
    logSwitch() {
      if (this.ruleForm.switch == "跟随系统设置") {
        this.switchLog = 0;
        this.ruleForm.switchLimit = "";
        this.four = true;
      } else if (this.ruleForm.switch == "关") {
        this.switchLog = 1;
        this.ruleForm.switchLimit = "";
        this.four = true;
      } else if (this.ruleForm.switch == "开") {
        this.switchLog = 2;
        this.four = false;
      }
    },
    //调度员app日志记录开关
    appLog() {
      if (this.ruleForm.log == "跟随系统设置") {
        this.logApp = 0;
        this.ruleForm.appLogLimit = "";
        this.five = true;
      } else if (this.ruleForm.log == "关") {
        this.logApp = 1;
        this.ruleForm.appLogLimit = "";
        this.five = true;
      } else if (this.ruleForm.log == "开") {
        this.logApp = 2;
        this.five = false;
      }
    },
    //企业存储空间报警配置
    savation() {
      if (this.ruleForm.save == "跟随系统设置") {
        this.saveSet = 0;
        this.ruleForm.saveLimit = "";
        this.six = true;
      } else if (this.ruleForm.save == "自定义") {
        this.saveSet = 1;
        this.six = false;
      }
    },
    inputOnInput(e) {
      this.$forceUpdate();
    },
    //百度地图
    handler({ BMap, map }) {
      var point = new BMap.Point(104.081548, 30.652613);
      map.centerAndZoom(point, 16);
    },
    getGps(results) {
      console.log(results.Br[0].point);
      this.gpsLocation = results.Br[0].point;
    },
    //添加企业
    async add() {
      let name = this.ruleForm.bname;
      let acount = this.ruleForm.mainAccount;
      this.addrDetail = this.ruleForm.address;
      let addrDetail = this.addrDetail;
      let charge = this.ruleForm.personInCharge;
      let phone = this.ruleForm.picPhone;
      let dateTime = this.ruleForm.validityTime + " " + "00:00:00";
      let coStatus = this.staus;
      let others = this.ruleForm.description;
      let limit = this.ruleForm.deviceNumLimit;
      let voice = this.ruleForm.voice;
      let see = this.ruleForm.see;
      let talk = this.ruleForm.talk;
      let rail = this.ruleForm.rail;
      let track = this.ruleForm.track;
      let broad = this.ruleForm.broad;
      let detect = this.ruleForm.detect;
      let time = this.videoTime;
      let timeLimit = this.ruleForm.timeLimit;
      let uploadVideo = this.uploadVideo;
      let videoLimit = this.ruleForm.videoLimit;
      let appVideo = this.appVideo;
      let appLimit = this.ruleForm.appLimit;
      let switchLog = this.switchLog;
      let switchLimit = this.ruleForm.switchLimit;
      let logApp = this.logApp;
      let appLogLimit = this.ruleForm.appLogLimit;
      let saveSet = this.saveSet;
      let saveLimit = this.ruleForm.saveLimit;
      //取出bid和did
      let didUpdate = this.did;
      let idUpdate = JSON.parse(sessionStorage.getItem("updateID"));
      let lon;
      let lat;
      if ((this.changelocationflag = true)) {
        lon = this.gpsLocation.lng;
        lat = this.gpsLocation.lat;
      } else {
        lon = this.setlongitude;
        lat = this.setlatitude;
      }
      console.log(lon, lat);

      const data = await this.updateCompanyAsync({
        bname: name,
        mainAccount: acount,
        address: addrDetail,
        personInCharge: charge,
        picPhone: phone,
        validityTime: dateTime,
        status: coStatus,
        description: others,
        deviceNumLimit: limit,
        isCall: voice,
        vedio: see,
        talkback: talk,
        mapRange: rail,
        trail: track,
        broadcast: broad,
        listener: detect,
        vedioTimeLimitSys: time,
        vedioTimeLimit: timeLimit,
        isAutoUploadSys: uploadVideo,
        isAutoUpload: videoLimit,
        dispatcherUploadLimitSys: appVideo,
        dispatcherUploadLimit: appLimit,
        appLogSys: switchLog,
        appLog: switchLimit,
        dispatcherLogSys: logApp,
        dispatcherLog: appLogLimit,
        storeTipSys: saveSet,
        storeTip: saveLimit,
        bid: idUpdate,
        did: didUpdate,
        longitude: lon,
        latitude: lat
      });
      if (data.code == 200) {
        this.$message("修改成功");
        this.$router.push(`/Index/company`);
        this.formAsync();
      } else if (data.code == 501) {
        this.$message("电话号码格式错误");
      } else if (data.code == 502) {
        this.$message("向企业后台输入数据失败");
      } else {
        this.$message("修改失败");
      }
    }
  }
};
</script>

<style scoped>
.updateContainer {
  width: 96%;
  margin: 0 auto;
}
.form {
  width: 100%;
  height: 770px;
  overflow-y: auto;
  padding-top: 15px;
  background-color: #ffffff;
}
.button {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  width: 140px;
  height: 40px;
  text-align: center;
  padding: 10px 15px;
  margin-left: 123px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.button:hover {
  cursor: pointer;
}
.form input {
  margin-left: 30px;
  -web-kit-appearance: none;
  -moz-appearance: none;
  font-size: 14px;
  width: 85px;
  height: 32px;
  border-radius: 4px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border: 1px solid #c8cccf;
  color: rgba(153, 153, 153, 1);
  padding-left: 10px;
}
.form span {
  display: inline-block;
  margin-left: 10px;
}
.form i {
  display: inline-block;
  font-style: normal;
  margin-left: 30px;
  color: rgba(153, 153, 153, 1);
}
.form p {
  color: rgba(153, 153, 153, 1);
}
.form .el-form .finalVideo {
  display: flex;
}
/* 地图容器 */
#l-map {
  width: 400px;
  height: 300px;
  margin-top: 20px;
}
.address {
  margin-top: 20px;
