<template>
  <div class="information">
    <p>消息中心</p>
    <div class="table">
      <el-table :data="list" border style="width: 100%">
        <el-table-column prop="mid" label="id" v-if="show"></el-table-column>
        <el-table-column type="index" width="50"></el-table-column>
        <el-table-column prop="title" label="消息名称" width="300px"></el-table-column>
        <el-table-column prop="bname" label="企业名称" width="300px"></el-table-column>
        <el-table-column prop="content" label="消息内容"></el-table-column>
        <el-table-column prop="ctime" label="消息发送时间" width="200px"></el-table-column>
      </el-table>
      <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        :page-size="pageSize"
        :current-page="pageNum"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("system");
export default {
  data() {
    return {
      show: false,
      arr: []
    };
  },
  async mounted() {
    
    this.mesCenterAsync();
    await this.getStatus();
    this.changeStatus();
    
  },
  computed: {
    ...mapState(["pageNum", "pageSize", "list", "total"])
  },
  methods: {
    ...mapActions([
      "mesCenterAsync",
      "changePageAsync",
      "deletAsync",
      "newsStatusAsync"
    ]),
    //分页
    handleCurrentChange(val) {
      this.changePageAsync(val);
    },
    //消息已读
    async getStatus() {
      const data = await this.newsStatusAsync();
      this.arr = data;
    },
    changeStatus() {
      let arr = this.arr;
      let aaa = JSON.stringify(arr);
      this.deletAsync({
        mids: aaa
      });
      this.$router.push(`/Index/informationCenter`);
    }
  }
};
</script>

<style scoped>
.information {
  width: 96%;
  height: 100%;
  margin: 0 auto;
}
.information > p {
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 1px solid rgb(192, 188, 188);
}

.el-pagination {
  text-align: center;
  margin-top: 15px;
}
</style>