import infoApi from "../services/infoService";

export default {
    namespaced: true,
    state: {
        pageNum: 1,
        pageSize: 5,
        status:'',
        list: [],
        total: 4,
    },
    mutations: {
        setData(state, payload) {
            //total整合总条数
            Object.assign(state, {
                list: payload.repMap.list,
                total:payload.count
            })
        },
        changePage(state, payload) {
            Object.assign(state, {
                pageNum: payload
            })
        },

    },
    actions: {
        //列表数据获取
        async allCompanyAsync(context, payload) {
            const {data} = await infoApi.allCompany(context.state);
            context.commit("setData", data)
            return data;
        },
        //分页
        async changePageAsync(context, payload) {
            context.commit("changePage", payload);
            console.log(context.state.bname);
            
            const params = {
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum,
                status:context.state.status,
            };            
            const {data} = await infoApi.allCompany(params);
            context.commit("setData", data);
        },
    }
}