import dictionaryApi from "../services/dictionaryService";

export default {
    namespaced: true,
    state: {
        pageNum: 1,
        pageSize: 6,
        list: [],
        total: 4,
    },
    mutations: {
        setData(state, payload) {
            //total整合总条数            
            Object.assign(state, {
                list: payload.dicList.list,
                total: payload.count
            })
        },
        changePage(state, payload) {
            Object.assign(state, {
                pageNum: payload
            })
        },
    },
    actions: {
        //列表数据获取
        async dictionaryFormAsync(context) {
            const params = {
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum
            };
            const {
                data
            } = await dictionaryApi.dictionaryForm(params);
            context.commit("setData", data)
            return data;
        },
        //分页
        async changePageAsync(context, payload) {
            context.commit("changePage", payload);
            const params = {
                pageSize: context.state.pageSize,
                pageNum: context.state.pageNum
            };
            const {
                data
            } = await dictionaryApi.dictionaryForm(params);
            context.commit("setData", data);
        },
        //字典--禁用
        async reStartAsync(context, payload) {
            const data = await dictionaryApi.reStart(payload);
            return data;
        },
        //新增
        async additionAsync(context, payload) {
            const data = await dictionaryApi.addition(payload);
            return data;
        },
        //获取单条数据
        async detailAsync(context, payload) {
            const data = await dictionaryApi.detail(payload);
            return data;
        },
        //修改
        async updateAsync(context, payload) {
            const data = await dictionaryApi.update(payload);
            return data;
        },
    }
}