<template>
  <div class="dictionaryContainer">
    <p>字典配置</p>
    <div class="dictionaries">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="终端设备型号" name="first" class="first">
          <div class="first">
            <button class="add" type="text" @click="dialogDictionary = true;">添加</button>
            <el-dialog title="添加终端设备型号" :visible.sync="dialogDictionary">
              <el-form :model="form">
                <el-form-item label="选项名称" :label-width="formLabelWidth" required>
                  <el-input
                    v-model="form.name"
                    autocomplete="off"
                    placeholder="限20字符"
                    class="limit"
                  ></el-input>
                </el-form-item>
                <el-form-item label="状态" :label-width="formLabelWidth" required>
                  <el-select v-model="addValue" placeholder="请选择状态">
                    <el-option
                      :options="addOption"
                      v-for="item in addOption"
                      :key="item.addValue"
                      :label="item.label"
                      :value="item.addValue"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button @click="dialogDictionary = false">取 消</el-button>
                <el-button type="primary" @click="dialogDictionary = false;addition()">确 定</el-button>
              </div>
            </el-dialog>
            <div class="table">
              <el-table :data="list" border style="width: 100%">
                <el-table-column type="index" width="50" label="排序"></el-table-column>
                <el-table-column prop="did" label="ID" v-if="show"></el-table-column>
                <el-table-column prop="option_name" label="选项名称"></el-table-column>
                <el-table-column prop="flag" label="状态" :formatter="changeStatus"></el-table-column>
                <el-table-column fixed="right" label="操作">
                  <template slot-scope="scope">
                    <el-button
                      type="text"
                      size="small"
                      @click="updateDictionary = true;change(scope.row.did)"
                    >编辑</el-button>

                    <el-button
                      type="text"
                      size="small"
                      v-if="flagStatus==scope.row.flag"
                      @click="stop = true;repause(scope.row.did)"
                    >禁用</el-button>
                    <el-button
                      type="text"
                      size="small"
                      v-else
                      @click="startUse = true;restart(scope.row.did)"
                    >启用</el-button>
                    <el-dialog title="启用" :visible.sync="startUse" append-to-body width="30%">
                      <span>是否启用？</span>
                      <div slot="footer" class="dialog-footer">
                        <el-button @click="startUse = false">取 消</el-button>
                        <el-button type="primary" @click="startUse = false;start();">确 定</el-button>
                      </div>
                    </el-dialog>
                    <el-dialog title="禁用" :visible.sync="stop" width="30%" append-to-body>
                      <span>是否禁用？</span>
                      <span slot="footer" class="dialog-footer">
                        <el-button @click="stop = false">取 消</el-button>
                        <el-button type="primary" @click="stop = false;pause()">确 定</el-button>
                      </span>
                    </el-dialog>
                    <el-dialog title="修改终端设备型号" :visible.sync="updateDictionary" append-to-body>
                      <el-form>
                        <el-form-item label="选项名称" :label-width="formLabelWidth" required>
                          <el-input
                            v-model="updateName"
                            autocomplete="off"
                            placeholder="限20字符"
                            class="limit"
                          ></el-input>
                        </el-form-item>
                        <el-form-item label="状态" :label-width="formLabelWidth" required>
                          <el-select v-model="updateValue" placeholder="请选择状态">
                            <el-option
                              :options="updateOption"
                              v-for="item in updateOption"
                              :key="item.updateValue"
                              :label="item.label"
                              :value="item.updateValue"
                            ></el-option>
                          </el-select>
                        </el-form-item>
                      </el-form>
                      <div slot="footer" class="dialog-footer">
                        <el-button @click="updateDictionary = false">取 消</el-button>
                        <el-button type="primary" @click="updateDictionary = false;updates()">确 定</el-button>
                      </div>
                    </el-dialog>
                  </template>
                </el-table-column>
              </el-table>
              <el-pagination
                background
                layout="prev, pager, next"
                :total="total"
                :page-size="pageSize"
                :current-page="pageNum"
                @current-change="handleCurrentChange"
              ></el-pagination>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("dictionary");
export default {
  data() {
    return {
      activeName: "first",
      dialogDictionary: false,
      form: {
        name: "",
        addValue: ""
      },
      addValue: "",
      formLabelWidth: "120px",
      show: false,
      startUse: false,
      stop: false,
      flagStatus: false,
      addOption: [
        {
          addValue: 0,
          label: "启用"
        },
        {
          addValue: 1,
          label: "禁用"
        }
      ],
      updateDictionary: false,
      updateName: "",
      updateValue: "",
      updateOption: [
        {
          updateValue: 0,
          label: "启用"
        },
        {
          updateValue: 1,
          label: "禁用"
        }
      ]
    };
  },
  mounted() {
    this.dictionaryFormAsync();
  },
  computed: {
    ...mapState(["pageNum", "pageSize", "list", "total"])
  },
  methods: {
    ...mapActions([
      "dictionaryFormAsync",
      "changePageAsync",
      "reStartAsync",
      "additionAsync",
      "detailAsync",
      "updateAsync"
    ]),
    handleClick(tab, event) {
      console.log(tab, event);
    },
    //分页
    handleCurrentChange(val) {
      this.changePageAsync(val);
    },
    changeStatus(row, column) {
      switch (row.flag) {
        case 0:
          return "已启用";
          break;
        case 1:
          return "已禁用";
          break;
      }
    },
    //启用和禁用
    repause(prop) {
      this.pauseID = prop;
    },
    restart(prop) {
      this.startID = prop;
    },
    async start() {
      let id = this.startID;
      const data = await this.reStartAsync({
        dicId: id
      });
      if (data.code === 200) {
        this.flagStatus == true;
        this.$message("启用成功");
        this.dictionaryFormAsync();
      } else {
        this.$message("修改失败，联系管理员");
      }
    },
    async pause() {
      let id = this.pauseID;
      const data = await this.reStartAsync({
        dicId: id
      });
      if (data.code === 200) {
        this.flagStatus == false;
        this.$message("禁用成功");
        this.dictionaryFormAsync();
      } else {
        this.$message("修改失败，联系管理员");
      }
    },
    //添加
    async addition() {
      let name = this.form.name;
      let status = this.addValue;
      const data = await this.additionAsync({
        optionName: name,
        flag: status
      });
      if (data.code === 200) {
        this.form.name = "";
        this.addValue = "";
        this.$message("新增成功");
        this.dictionaryFormAsync();
      } else if (data.code === 213) {
        this.$message("选项名字重复，请重新输入");
      } else {
        this.form.name = "";
        this.addValue = "";
        this.$message("新增失败，联系管理员");
      }
    },
    //修改
    async change(prop) {
      let id = prop;
      const data = await this.detailAsync({
        dicId: id
      });
      this.updateName = data.data.optionName;
      this.updateValue = data.data.flag;
      this.updateId = prop;
    },
    async updates() {
      let name = this.updateName;
      let status = this.updateValue;
      let updateId = this.updateId;
      const data = await this.updateAsync({
        optionName: name,
        flag: status,
        did: updateId
      });
      if (data.code === 200) {
        this.$message("修改成功");
        this.dictionaryFormAsync();
      } else if (data.code === 213) {
        this.$message("选项名字重复，请重新输入");
      } else {
        this.$message("修改失败，联系管理员");
      }
    }
  }
};
</script>

<style scoped>
.dictionaryContainer {
  width: 96%;
  height: 900px;
  margin: 0 auto;
}
.dictionaryContainer > p {
  font-size: 20px;
  font-weight: 600;
}
.first {
  width: 100%;
  height: 800px;
  overflow-y: auto;
}
.el-pagination {
  text-align: center;
  margin-top: 15px;
}
.add {
  border: 1px solid transparent;
  outline: none;
  font-size: 14px;
  height: 34px;
  width: 80px;
  padding: 0;
  text-align: center;
  margin: 0px 12px 13px 1550px;
  font-family: Microsoft YaHei;
  font-weight: 400;
  border-radius: 4px;
  background-color: rgba(21, 150, 255, 1);
  color: #ffffff;
}
.add:hover {
  cursor: pointer;
}
.el-form .limit {
  width: 600px;
}
</style>
