<template>
  <div class="loginBox">
    <div class="bottom">
      <div></div>
      <div></div>
    </div>
    <div class="login">
      <div class="top">
        <div class="top-right"></div>
        <div class="top-left">
          <div class="welcome">
            <span>欢迎登录系统</span>
            <div>
              <i class="img1"></i>
              <input type="text" placeholder="请输入账号" v-model="user.username" />
            </div>
            <div>
              <i class="img2"></i>
              <input type="password" placeholder="请输入密码" v-model="user.password"  />
            </div>
            <div class="code">
              <div>
                <i class="img3"></i>
                <input type="text" placeholder="图形验证码" class="authcode" v-model="user.authCode" />
              </div>
              <div>
                <img class="img4" :src="imgUrl" @click="refreshCode" />
              </div>
            </div>
            <button @click="login">立即登录</button>
            <p>copyright©四川倚天华安科技有限公司</p>
            <p>技术支持：成都四象联创科技有限公司</p>
            <p>备注：系统操作页面最佳分辨率为：1080P，建议采用此分辨率下的显示设备进行系统的登录与操作应用。</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";
const { mapState, mapActions } = createNamespacedHelpers("admin");
export default {
  data() {
    return {
      user: {
        username: "",
        password: "",
        authCode: ""
      },
      imgUrl: ""
    };
  },
  mounted() {
    this.getCode();
  },
  computed: {
    ...mapState(["message", "code", "data"])
  },
  methods: {
    ...mapActions(["authCodeAsync", "loginAsync"]),
    async login() {
      await this.loginAsync({
        userName: this.user.username,
        password: this.user.password, 
        code: this.user.authCode
      });
      if (this.code === 200) {
        this.$router.push(`/Info`);
        this.$message("登录成功");        
        sessionStorage.setItem("name", this.data.uname);        
      } else if (this.code === 201) {
        this.$message("密码用户名验证码为空，请重新输入");
      } else if (this.code === 202) {
        this.$message("账号错误，请重新输入");
      } else if (this.code === 203) {
        this.$message("验证码不匹配，请重新输入");
      } else if (this.code === 204) {
        this.$message("密码错误，请重新输入");
      }
    },
    async getCode() {
      const img = await this.authCodeAsync();
      this.imgUrl = img;
    },
    refreshCode() {
      this.getCode();
    }
  }
};
</script>

