import adminApi from "../services/adminService";

export default {
    namespaced: true,
    state: {
        message: "",
        code: "",
        data:""
    },
    mutations: {
        setLogin(state,payload){
            Object.assign(state,payload)
        }
    },
    actions: {
        async loginAsync(context, payload) {
            const {data} = await adminApi.login(payload);            
            context.commit("setLogin", data)
        },
        async authCodeAsync(context, payload) {
            const data = await adminApi.authCode(context.state);
            return data;
        },
    }
}